import { Injectable } from '@angular/core';
import { Router } from '@angular/router';
import { VhAuth, VhQuerySales } from 'ionic-vhframeworks';
import { NzModalService } from 'ng-zorro-antd/modal';
import { DateService } from './date.service';
import { VhComponent } from '../components/vh-component/vh-component';
import { LanguageService } from './language.service';


@Injectable({
  providedIn: 'root'
})
export class UserService {

  constructor(
    private vhAuth: VhAuth,
    private vhQuerySales: VhQuerySales,
    private nzModalService: NzModalService,
    private router: Router,
    private dateService: DateService,
    private vhComponent: VhComponent,
    private languageService: LanguageService,
  ) {}


  
  /**
   * Kiểm tra xem tài khoản đã hết hạn chưa.
   * Nếu > 3 ngày thì check xem còn trong hạn được phép sử dụng hay không, 
   * nếu trong hạn thì hiện thông yêu cầu gia hạn, 
   * ngược lại thông báo tài khoản đã tạm khóa
   */
  checkExpireAccount() {
    let number = this.dateService.getNumberOfDay(new Date(), new Date(this.vhQuerySales.getLocalRestrictionMainPackage().expire_date));
    if (number > 3) {// check sau 3 ngày hết hạn hiện thông báo
      // check tiếp  còn trong hạn được phép sử dụng hay ko
      if (number < this.vhQuerySales.getLocalRestrictionMainPackage().extra_usage_duration) {
        this.nzModalService.confirm({
          nzTitle: this.languageService.translate('Branch') + ': ' + this.vhQuerySales.getDefaultBranch().name + ' ' + this.languageService.translate('has expired') + '. ' + this.languageService.translate('Please renew to continue') + '!',
          nzOkText: this.languageService.translate('Pay'),
          nzCancelText: this.languageService.translate('Continue'),
          nzClosable: false,
          nzCloseIcon: null,
          nzOnOk: () => {
            // go to thanh toán
            this.vhQuerySales.getPayments(
              { id_store: { $eq: this.vhAuth.getUser()._id }, payment_type: { $eq: 5 } },
              {},
              {},
              0
            ).then((payments: any) => {
              if (payments) {
                if (payments.length > 0) {
                  this.router.navigate(['/sales/payment/detail'],
                    {
                      state: payments
                    });
                } else if (payments == 0) {
                  this.router.navigate(['/sales/payment']);
                }
              }
            });
          },
          nzOnCancel: () => {
          }
        });
      }
      else {
        this.nzModalService.confirm({
          nzTitle: this.languageService.translate('Your current branch has been temporarily locked. Please renew to continue') + '!',
          nzOkText: this.languageService.translate('Pay'),
          nzCancelText: this.languageService.translate('Exit'),
          nzCloseIcon: null,
          nzClosable: false,
          nzOnOk: () => {
            // go to thanh toán
            this.vhQuerySales.getPayments(
              { id_store: { $eq: this.vhAuth.getUser()._id }, payment_type: { $eq: 5 } },
              {},
              {},
              0
            ).then((payments: any) => {
              if (payments) {
                if (payments.length > 0) {
                  this.router.navigate(['/sales/payment/detail'],
                    {
                      state: payments
                    }
                  );
                } else if (payments == 0) {
                  this.router.navigate(['/sales/payment']);
                }
              }
            });
          },
          nzOnCancel: () => {
            this.router.navigate(["/login"]);
          }
        });
      }
    }
  }



  /**
   * Đăng xuất khỏi app và navigate về trang login
   */
  logout() {
    this.vhAuth.signOut().then(() => {
      this.vhQuerySales.closeSyncCollections();
      this.vhQuerySales.clearDefaultBranch(); // xóa branchDefault trước khi thoát app
      this.router.navigate(["/login"]);
    }, error => {
      console.error('error', error);
    });
  }



  /**
   * Chuyển qua Trang quản trị và cập nhật lại default_display_page của user
   * @param type sẽ chuyển qua trang nào. /sales/${type}
   * @param permission
   * @example this.updateInterface('dashboard', 'admin_display_page');
   */
  public updateInterface(type, permission) {
    if (this.vhAuth.checkMyPermission(permission)) {
      Promise.all([
        this.vhComponent.showLoading(this.languageService.translate("Changing interface..."), "current-loading", null, null, false),
        this.vhAuth.updateMyPermission({ default_display_page: permission }),
      ]).then(() => {
        this.vhComponent.hideLoading(0);
        setTimeout(() => {
          this.router.navigate(['/sales/' + type], { state: { isSync: true } });
        }, 200);
      });
    }
    this.vhAuth.localStorageSET("isFirstTime_sales_pos", true);
  }



  /**
   * Thiết lập giao diện trang bán hàng mặc định khi mở app.
   * Lấy từ template trong main_sector. sales-pos, sales-desktop, sales-retail,...
   * @param default_selling_page trang bán hàng mặc định; sales-pos, sales-desktop, sales-retail,...
   */
  updateSalesInterface(default_selling_page) {
    this.vhComponent.showLoading('').then(() => {
      this.vhAuth.updateMyPermission({ default_selling_page: default_selling_page, default_display_page: 'selling_display_page' })
        .then((bool) => {
          this.vhComponent.hideLoading(0)
            .then(() => {
              this.router.navigate(['/sales/' + default_selling_page], { state: { isSync: true } });
            });
        });
    });
  }

}
