import { ChangeDetectorRef, Component, ElementRef, EventEmitter, Output, ViewContainerRef } from '@angular/core';
import { SalesRetailUI } from '../../../sales-retail';
import { SalesRetailSharedService } from '../../../sales-retail-shared.service';
import { VhComponent } from 'src/app/sales/components/vh-component/vh-component';
import { LanguageService } from 'src/app/sales/services/language.service';
import { VhAlgorithm, VhAuth, VhQuery, VhQuerySales ,VhEinvoice} from 'ionic-vhframeworks';
import { Router } from '@angular/router';
import { NzMessageService } from 'ng-zorro-antd/message';
import { FunctionService } from 'src/app/sales/services/function.service';
import { NzModalService } from 'ng-zorro-antd/modal';
import { Platform } from '@ionic/angular';
import { BranchService } from 'src/app/sales/services/branch.service';
import { CategoryService } from 'src/app/sales/services/category.service';
import { ProductService } from 'src/app/sales/services/product.service';
import { ThemeService } from 'src/app/sales/services/theme.service';
import { UserService } from 'src/app/sales/services/user.service';
import { BillService } from 'src/app/sales/services/bill.service';
import { CustomerService } from 'src/app/sales/services/customer.service';
import { PrintService } from 'src/app/sales/services/print.service';
import { ProvinceService } from 'src/app/sales/services/province.service';
import { CleaveJsService } from 'src/app/sales/services/cleave-js.service';
import { CountryCode } from 'libphonenumber-js/max';

@Component({
  selector: 'app-add-customer',
  templateUrl: './add-customer.component.html',
  styleUrls: [
    './add-customer.component.scss',
    './add-customer-custom.component.scss',
  ]
})
export class AddCustomerComponent extends SalesRetailUI {

  @Output() onClose = new EventEmitter<any>();

  /** Danh sách Khu vực */
  provinces: any = [];
  /** Danh sách Phường/Xã */
  districts: any = [];
  /** Mã Khu vực */
  provinceCode: number = 1;


  constructor(
    protected salesSapoSharedService: SalesRetailSharedService,
    protected vhComponent: VhComponent,
    public languageService: LanguageService,
    public vhAuth: VhAuth,
    public vhQuerySales: VhQuerySales,
    protected router: Router,
    protected mess: NzMessageService,
    protected functionService: FunctionService,
    protected nzModalService: NzModalService,
    protected plaform: Platform,
    public vhAlgorithm: VhAlgorithm,
    protected branchService: BranchService,
    public categoryService: CategoryService,
    protected productService: ProductService,
    public themeService: ThemeService,
    public userService: UserService,
    public billService: BillService,
    protected customerService: CustomerService,
    protected cdr: ChangeDetectorRef,
    protected viewContainerRef: ViewContainerRef,
    protected vhQuery: VhQuery,
    protected printService: PrintService,
    protected element: ElementRef,
    protected provinceService: ProvinceService,
    protected cleaveJsService: CleaveJsService,
    protected vhEinvoice: VhEinvoice,
  ) {
    super(
      salesSapoSharedService, vhComponent, languageService, vhAuth, vhQuerySales, router, mess, functionService, nzModalService,
      plaform, vhAlgorithm, branchService, categoryService, productService, themeService, userService,
      billService, customerService, cdr, viewContainerRef, vhQuery, printService, cleaveJsService, vhEinvoice
    );
  }



  ngOnInit() {
    this.provinces = this.provinceService.getProvinces().map((item: any) => {
      return {
        value: item.code,
        label: item.name,
      }
    });
  }



  /**
   * Khi thay đổi Khu vực
   */
  onChangeProvince() {
    this.districts = this.provinceService.getDistricts(this.provinceCode)?.map((item: any) => {
      return {
        value: item.code,
        label: item.name,
      }
    });
  }



  /**
   * Khi add customer.
   * 
   * Sẽ validate form trước khi thêm.
   */
  onAddCustomer() {
    if(this.addCustomerForm.invalid) {
      this.addCustomerForm.markAllAsTouched();
      this.addCustomerForm.markAsDirty();

      if(this.addCustomerForm.controls['name']?.['errors']?.['required']) {
        this.vhComponent.showToast(1500, this.languageService.translate('Customer name is required'), 'alert-toast');
      }
      if(this.addCustomerForm.controls['phone']?.['errors']?.['required']) {
        this.vhComponent.showToast(1500, this.languageService.translate('Phone number is required'), 'alert-toast');
      }
      if(this.addCustomerForm.controls['phone']?.['errors']?.['invalidPhone']) {
        this.vhComponent.showToast(1500, this.languageService.translate('_Invalid phone number'), 'alert-toast');
      }
      if(this.addCustomerForm.controls['email']?.['errors']?.['email']) {
        this.vhComponent.showToast(1500, this.languageService.translate('Invalid email format'), 'alert-toast');
      }
      if(this.addCustomerForm.controls['taxcode']?.['errors']?.['required']) {
        this.vhComponent.showToast(1500, this.languageService.translate('Tax code is required'), 'alert-toast');
      }
    }
    else {
      this.addCustomer();
      this.onClose.emit();
    }
  }



  /**
   * Mở modal chọn mã quốc gia
   */
  openSearchCountryCode() {
    this.functionService.openSearchCountryCode().then((data) => {
      this.countryCode = data.code;
      this.callingCode = data.callingCode;
      this.formattedPhone = this.functionService.getFormattedPhoneNumber(this.addCustomerForm.value.phone, this.countryCode as CountryCode);
    }).catch((error) => {
      console.error('Error opening country code search:', error);
    });
  }



  /**
   * Format số điện thoại khi nhập và set vào form
   * @param event Sự kiện khi nhập số điện thoại
   * @param formName tên của from cần dùng
   * @param fieldName tên của field chứa số điẹn thoại
   */
  formatPhoneNumber(event: Event): void {
    // Lấy số điện thoại đã format
    const input = (event.target as HTMLInputElement).value;
    this.formattedPhone = this.functionService.getFormattedPhoneNumber(input, this.countryCode as CountryCode);
    // Cập nhật vào form
    this.addCustomerForm.get('phone')?.setValue(this.formattedPhone, { emitEvent: false });
  }

}
