import { Injectable } from '@angular/core';
import { FunctionService } from '../services/function.service';
import { VhAuth, VhQuerySales } from 'ionic-vhframeworks';
import { FormGroup } from '@angular/forms';
import { NzCascaderOption } from 'ng-zorro-antd/cascader';
import { LanguageService } from '../services/language.service';
import { VhSaleInvoiceDetailEdit } from '../components/bill-type/bill-type-1/sales-desktop/sales-desktop-interface';
import { Subscription } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class SalesRetailSharedService {

  useBranch: boolean = this.functionService.checkUsingBranch();

  /** Danh sách hoá đơn */
  listBills: any[] = [];

  /** Danh sách chi tiết hoá đơn */
  billDetails: any[] = [];

  /** index hoá đơn đang được chọn */
  selectedIndex: number = 0;

  /** Hoá đơn đang được chọn */
  selectedBill: any;

  /** Đã load xong billToday hay chưa để check hiển thị loading */
  isLoadedBillToday = false;

  /** Có đang thêm bill hay không */
  isAddingBill = false;

  /** Show modal cấu hình Cài đặt trang bán hàng -> Khác */
  showConfig = false;

  /** Show modal cấu hình Cài đặt trang bán hàng -> Trang in */
  showConfigPrint = false;

  /** Show modal cấu hình Cài đặt trang bán hàng -> Hiển thị */
  showConfigSales = false;

  /** giá bán: 1-giá bán lẻ, 2-giá bán buôn/bán sỉ */
  price_type: any;

  /** Có hiện modal chọn Giao diện bán hàng hay không */
  isShowChooseInterface: boolean = false;

  /** Giá bán mặc định; 1-giá bán lẻ, 2-giá bán buôn/bán sỉ */
  default_price_type = 1;

  /** Lưu thông tin nhân viên, khách hàng, delivery, payment của bill hiện tại */
  billInfo: any = {};

  /** cho phép nợ hay không */
  debt: boolean = false;

  /** id chi nhánh */
  id_branch = this.vhQuerySales.getDefaultBranch()._id;

  /** Giao diện bán hàng mặc định */
  default_selling_page = this.vhAuth.getLocalMyPermissionName('default_selling_page');

  /** Cho phép sửa ngày tạo hóa đơn hay không */
  sales_enable_edit_create_date = true;

  /** Load bill xong hay chưa */
  isLoadedBill = false;



  /** Theo dõi sự kiện nhấn trên bàn phím */
  event: any;



  /** Danh sách Hạng khách hàng */
  listCustomerGroups: any = this.vhQuerySales.getlocalCustomerClasss();

  /** Danh sách chương tình tích điểm hóa đơn */
  listEarningPointBills: any = [];

  /** Danh sách chương trình tích điểm sản phẩm */
  listEarningPointsProducts: any = [];

  /** Hạng khách hàng của khách được chọn */
  selectedCustomerClass: any;

  /** Chương trình tích điểm hóa đơn của khách được chọn */
  selectedCustomerProgramBill: any;

  /** Chương trình tích điểm sản phẩm của khách được chọn */
  selectedCustomerProgramProduct: any;

  /** Điểm tích lũy hiện tại */
  pointValidity;



  /** Danh sách chi nhánh */
  branchList: Array<any> = [];

  /** Chi nhánh mặc định */
  branchDefault: any = {};



  /** Có hiện modal Thông tin tài khoản hay không */
  isShowAccountInfo: boolean = false;

  /** Thông tin tài khoản employee hiện tại */
  employee: any = this.vhAuth.getUser();



  /** index của tab category được chọn trong Best selling */
  indexTabBestSellingCategory: any = 0;

  /** Danh sách sản phẩm ở tab Sản phẩm trong Best selling */
  productsBestSelling: any[] = [];

  /** Danh sách sản phẩm phân trang Best Selling */
  productsPaginationBestSelling: any[] = [];

  /** Danh sách dịch vụ ở tab Dịch vụ trong Best selling */
  servicesBestSelling: any[] = [];

  /** Danh sách dịch vụ phân trang Best Selling */
  servicesPaginationBestSelling: any[] = [];



  /** Có hiện modal Tìm sản phẩm hay không */
  isShowSearchProduct: boolean = false;

  /** Danh sách tất cả sản phẩm */
  listAllProducts: any[] = [];

  /** Danh sách sản phẩm tìm kiếm, sẽ được hiển thị */
  searchListSearchProduct: any[] = [];

  /** Danh sách danh mục */
  listCategories: any[] = [];

  /** Từ khóa tìm kiếm sản phẩm */
  searchProductInput: string = '';

  /** index của tab category được chọn trong modal Search product */
  indexTabSearchProductCategory: any = 0;

  /** index của tab main sector được chọn trong modal Search product */
  indexTabSearchProductMainSector: any = 0;

  /** Giá trị ô search */
  searchProductValue: string = '';

  /** Setting - Tự động ẩn lô khi hết hạn sử dụng */
  auto_hidden_lot: boolean = this.vhQuerySales.getLocalAppSettingNameBranch('permission_branch').auto_hidden_lot;

  /** Setting - Tự động chọn lô có HSD gần nhất khi bán hàng */
  auto_select_lot: boolean = this.vhQuerySales.getLocalAppSettingNameBranch('permission_branch').auto_select_lot;



  /** Có phải hoá đơn được giảm giá hay không */
  isDiscountBill = false;

  /** Cộng tiền */
  subTotal = 0;

  /** Thuế (%) */
  taxValue = 0;

  /** % Giảm giá */
  percentDiscountBill: number;



  /** Debounce timer để xử lý addBillDetailItem liên tục */
  debounceAddBillDetailTimer: NodeJS.Timeout | null = null;



  /** Debounce khi thêm sản phẩm vào bill */
  addBillDetailQueue: Map<string, NodeJS.Timeout> = new Map();
  
  /** Setting - Tự động trỏ chuột vào ô số lượng lúc chọn/quét mã sản phẩm khi tạo hóa đơn */
  auto_active_quantity_input: boolean = this.vhQuerySales.getLocalAppSettingNameBranch('permission_branch').auto_active_quantity_input;



  
  /** Debounce timer để xử lý updateBill liên tục */
  debounceUpdateBillTimer: NodeJS.Timeout | null = null;



  /** Có hiện modal Thêm khách hàng hay không */
  isShowModalAddCustomer: boolean = false;

  /** Form thêm khách hàng */
  addCustomerForm: FormGroup;

  /** Trạng thái tạo khách hàng */
  createCustomerStatus: boolean = false;

  /** Danh sách khách hàng */
  customerList: any[] = [];

  /** Loại khách hàng. 1: Khách lẻ, 2: Thành viên */
  customerType: 1 | 2;

  /** Điểm tích lũy cộng thêm */
  earningPoint: number = 0;



  /** Trạng thái hiển thị modal Thêm thanh toán khác */
  showModalOtherDiscount: boolean = false;

  /** Mảng các coupon đã chọn */
  listSelectedCoupons: any = [];

  /** Số điểm sử dụng */
  valueDiscountPoint: number = 0;



  /** Danh sách (3)tài khoản */
  listWallets: Array<any> = [];

  /** Danh sách phương thức thanh toán */
  nzOptions: NzCascaderOption[] = [
    { value: 1, label: this.languageService.translate("Cash"), isLeaf: true },
    { value: 2, label: this.languageService.translate("Debit"), disabled: true, isLeaf: true },
    { value: 3, label: this.languageService.translate("Account"), children: [] }
  ];



  /** Biến tạm chứa sản phẩm khi chọn sản phẩm */
  tempProduct: VhSaleInvoiceDetailEdit;

  /** Có hiện modal chọn Lô hay không */
  isShowModalSelectLots = false;

  /** Có hiện modal chọn Lô trong Combo hay không */
  isShowModalSelectLotsCombo = false;

  /** Chứa tất cả Lô để search */
  listAllLots = [];

  /** Setting - Thông báo khi bán nguyên liệu đã hết hạn sử dụng */
  warning_lot_expried: boolean = this.vhQuerySales.getLocalAppSettingNameBranch('permission_branch').warning_lot_expried;



  /** Có hiện modal chọn Giá bán hay không */
  isShowModalChangePriceType = false;



  /** Đang thanh toán hay không */
  isPaying: boolean = false;

  /** Biến kiểm tra máy in */
  checkPrinter: boolean = false;

  /** Observable để kiểm tra máy in */
  observablePrinter: Subscription;


  
  /** Setting - Hiển thị đồng thời giá bán và giá khuyến mãi */
  display_promotion_selling_price: boolean = this.vhQuerySales.getLocalAppSettingNameBranch('permission_branch').display_promotion_selling_price;



  /** Đã chọn quà tặng chương trình khuyễn mãi type 7 (tặng quà theo tổng giá trị đơn hàng) hay chưa, true là chọn rồi, false là chưa */
  isType7GiftSelected = false;

  /** Có hiện modal Chọn sản phẩm tặng type 6 (tặng quà khi mua nhiều sản phẩm) hay không */
  isShowPromotionType6 = false;

  /** Có hiện modal Chọn sản phẩm tặng type 7 (tặng quà theo tổng giá trị đơn hàng) hay không */
  isShowPromotionType7 = false;

  /** Danh sách chương trình khuyến mãi type 7 (tặng quà theo tổng giá trị đơn hàng) hợp lệ sẽ hiển thị lên modal chọn. Ví dụ: [ {id_promotion: "naPGGvSGd7vRgwPDgaFV", name: "tong gia tri", pmtype: 7, products_gift: [{…}, {…}, {…}], promotions_maximum: 3, total_bill_value: 100000} ] */
  promotionsType7Show: any = [];

  /** Danh sách tất cả chương trình khuyến mãi type 7 (tặng quà theo tổng giá trị đơn hàng), để lọc ra và gán cái hợp lệ cho promotionsType7Show */
  promotionsType7: any = [];



  /** Có hiện modal chỉnh sửa đơn giá giảm giá hay không */
  isShowEditDiscountPrice: boolean = false;
  
  /** Ký hiệu tiền tệ. Ví dụ 'VND' */
  currencySymbol = JSON.parse(localStorage.getItem('vhsales_currencyFormat')).symbol.text;

  /** Loại giảm giá mặc định là % hoặc VND */
  default_discount_price_type = this.vhAuth.localStorageGET("default_discount_price_type") ? this.vhAuth.localStorageGET("default_discount_price_type").value : '%';

  /** Số tiền giảm giá % hoặc VND */
  discountValue = 0;

  /** Biến tạm lưu Đơn giá mới */
  tempDiscountPrice = 0;



  /** Để biết đã observableSyncOpeningBill hay chưa để có thể closeSyncOpeningBill mà không bị lỗi `ERROR TypeError: Cannot read properties of undefined (reading 'unsubscribe')`. Do lần đầu mới load thì chưa kịp observableSyncOpeningBill nên nếu nhấn selectBill (có chứa closeSyncOpeningBill) nhanh quá thì bị lỗi đó */
  alreadyObservableSyncOpeningBill: boolean = false;



  /** Có mở modal Chỉnh sửa giá hay không */
  isShowEditPrice: boolean = false;



  /** Mã quốc gia */
  countryCode: string = this.functionService.defaultCountryCode;
 
  /** Calling code */
  callingCode: string = this.functionService.defaultCallingCode;
  
  /** Số điện thoại đã format. VD: 333 333 333 */
  formattedPhone: string = '';



  /* ---------------------------------------------------------- DỊCH VỤ THỜI GIAN --------------------------------------------------------- */
  
  /** Thời gian mở bill, dùng để tính thời gian cho Dịch vụ thời gian */
  endTime: Date = new Date();
  /** Biến tạm lưu dịch vụ thời gian đang được chọn */
  selectedServiceTime: any;
  /** Có hiện modal Chỉnh sửa Dịch vụ thời gian hay không */
  isShowModalEditServiceTime: boolean = false;
  /** Có hiện modal Chỉnh sửa thời gian sử dụng hay không */
  isShowModalEditUsedTime: boolean = false;
  /** Biến tạm lưu thời gian bắt đầu (time_start_order) */
  tempStartTime: any;
  /** Biến tạm lưu thời gian kết thúc (time_end_order) */
  tempEndTime: any;



  /* -------------------------------------------------------------- GIAO HÀNG ------------------------------------------------------------- */

  /** Có hiện modal Chọn giao hàng hay không */
  isShowModalDelivery: boolean = false;



  constructor(
    private functionService: FunctionService,
    private vhAuth: VhAuth,
    private vhQuerySales: VhQuerySales,
    private languageService: LanguageService,
  ) { }

}