import { Component, ElementRef, EventEmitter, Input, Output, QueryList, SimpleChanges, ViewChild, ViewChildren } from '@angular/core';
import { FormArray, FormBuilder, FormControl, FormGroup, Validators } from '@angular/forms';
import { VhAlgorithm, VhQuerySales } from 'ionic-vhframeworks';
import { NzPopconfirmDirective } from 'ng-zorro-antd/popconfirm';
import { VhComponent } from 'src/app/sales/components/vh-component/vh-component';
import { LanguageService } from 'src/app/sales/services/language.service';
import { ProductService } from 'src/app/sales/services/product.service';

@Component({
  selector: 'app-modal-lot-list',
  templateUrl: './modal-lot-list.component.html',
  styleUrls: ['./modal-lot-list.component.scss']
})
export class ModalLotListComponent {
  @ViewChildren(NzPopconfirmDirective) popconfirms!: QueryList<NzPopconfirmDirective>;
  @ViewChild('blockFormLots') blockFormLots: ElementRef;
  /** Danh sách lô hiển thị */
  @Input() data: any[] = [];
  /** Có hiển thị hay không */
  @Input() isShowLotsList: boolean = false;
  /** Danh sách barcode */
  @Input() barcodes: any = [];
  /** Output khi đóng drawer */
  @Output() close = new EventEmitter<any>();
  @Output() clickButton = new EventEmitter<any>();

  /** Form group của danh sách lô */
  formLots = this.formBuilder.group({
    lots: this.formBuilder.array([], Validators.required)
  });

  /** Mảng danh sách lô. Mỗi lô bên trong là một form group */
  get lots() {
    return this.formLots.controls["lots"] as FormArray;
  }


  constructor(
    public vhAlgorithm: VhAlgorithm,
    private readonly formBuilder: FormBuilder,
    private vhQuerySales: VhQuerySales,
    private vhComponent: VhComponent,
    private lang: LanguageService,
    private productService: ProductService,
    private element: ElementRef,
  ) { }



  ngOnInit() {
    this.initAddLotsForm(this.data);
  }

  ngOnChanges(changes: SimpleChanges): void {
    if (changes.data && !changes.data.isFirstChange()) {
      this.formLots.reset();
      this.lots.clear();
      this.initAddLotsForm(changes.data.currentValue);
    }
  }



  /**
   * Khởi tạo form danh sách lô bằng cách thêm từng lô vào form
   * @param data
   */
  initAddLotsForm(data) {
    if (data.length > 0) {
      for (let i = 0; i < data.length; i++) {
        this.addLots(data[i]);
      }
    }
  }



  /**
   * Đóng drawer sẽ emit output close với giá trị false
   */
  onClose() {
    this.close.emit(false);
    this.formLots.reset();
    this.lots.clear()
  }



  /**
   * Thêm lô vào danh sách lô
   * @param lot lô cần thêm
   */
  addLots(lot?) {
    const lotForm = this.formBuilder.group({
      date_mfg: new FormControl(lot ? lot.date_mfg : new Date(new Date().setHours(0, 0, 0,)), Validators.required),
      date_exp: new FormControl(lot ? lot.date_exp : new Date(new Date().setHours(23, 59, 59,)), Validators.required),
      lot_number: new FormControl(lot ? lot.lot_number : '', Validators.required),
      hidden: new FormControl(lot ? lot.hidden : false),
      barcode: new FormControl(lot ? lot.barcode : ''),
    });
    this.lots.push(lotForm);
  }



  /**
   * Xoá lô theo index
   * @param index index của lô cần xoá
   */
  deleteLot(index: number) {
    this.lots.removeAt(index);
  }



  /**
   * Kiểm tra ngày sản xuất và ngày hết hạn hợp lý hay ko
   * @param lotForm
   */
  changeDate(lotForm) {
    // Nếu ngày sản xuất lớn hơn ngày hết hạn thì set ngày hết hạn bằng ngày sản xuất
    if (new Date(lotForm.value.date_mfg).getTime() >= new Date(lotForm.value.date_exp).getTime()) {
      lotForm.control['date_exp'].setValue(new Date(new Date(lotForm.value.date_mfg).setHours(23, 59, 59)));
      this.vhComponent.alertMessageDesktop("error", this.lang.translate("Staring date must be less than or the same as ending date"));
    }
  }



  /**
   * Khi nhấn nút Lưu
   */
  saveLots() {
    if ((this.blockFormLots.nativeElement.querySelector(`[id^='barcodeError']`) as HTMLElement)?.style.display == 'block') {
      this.vhComponent.alertMessageDesktop("error", this.lang.translate("Barcode already exists"));
    }
    else {
      let invalid_lot = this.lots.value.filter(item => new Date(item.date_exp).getTime() <= new Date(new Date(item.date_mfg)).getTime());
      if (invalid_lot.length) {
        this.vhComponent.alertMessageDesktop("error", invalid_lot[0].lot_number + ' ' + this.lang.translate("Staring date must be less than or the same as ending date"));
      }
      else {
        let data = this.lots.value.filter(item => item.lot_number != '' && new Date(item.date_exp).getTime() >= new Date(new Date(item.date_mfg)).getTime());
        this.close.emit(data);
        this.formLots.reset();
        this.lots.clear()
        this.isShowLotsList = false;
      }
    }
  }



  /**
   * Tạo barcode tự động tại vị trí position trong mảng
   * @param position
   */
  setBarcodeForLot(position) {
    let newbarcode = this.productService.generateBarcode();
    let detailproduct = this.vhQuerySales.getlocalDetailProduct_byBarcode(newbarcode);
    if (detailproduct == null) {
      (this.lots.at(position) as FormGroup).get('barcode').setValue(newbarcode);
      this.blockFormLots.nativeElement.querySelector('#barcodeError' + position).style.display = 'none';
    }
    else {
      this.blockFormLots.nativeElement.querySelector('#barcodeError' + position).style.display = 'block';
    }
  }



  /**
   * Kiểm tra barcode đã tồn tại hay chưa (cả trong local và sp đã tạo)
   * @param barcode barcode cần kiểm tra
   * @param position
   */
  checkBarcodeExist(barcode, position) {
    if (barcode.length) {
      const barcodeCounts = new Map<string, number>();
      // Đếm số lần xuất hiện của mỗi barcode
      this.lots.value?.forEach(lot => {
        const count = barcodeCounts.get(lot.barcode) || 0;
        barcodeCounts.set(lot.barcode, count + 1);
      });
      // Nếu số lần xuất hiện của barcode > 1 thì hiển thị lỗi
      this.lots.value?.forEach((lot, index) => {
        const isDuplicate = this.vhQuerySales.getlocalDetailProduct_byBarcode(lot.barcode) || (barcodeCounts.get(lot.barcode) && barcodeCounts.get(lot.barcode) > 1);
        this.blockFormLots.nativeElement.querySelector('#barcodeError' + index).style.display = isDuplicate ? 'block' : 'none';
      });
    }
    else {
      this.blockFormLots.nativeElement.querySelector('#barcodeError' + position).style.display = 'none';
    }
  }



  /**
   * Kiểm tra Số lô đã tồn tại hay chưa
   * @param lot tên lô cần check
   * @param position vị trí trong mảng
   */
  checkLotNumber(lot: string, position: number) {
    if (lot.length) {
      const lotCounts = new Map<string, number>();
      // Đếm số lần xuất hiện của mỗi lot_number
      this.lots.value?.forEach(lot => {
        const count = lotCounts.get(lot.lot_number) || 0;
        lotCounts.set(lot.lot_number, count + 1);
      });
      // Nếu số lần xuất hiện của lot_number > 1
      this.lots.value?.forEach((lot, index) => {
        const isDuplicate = (lotCounts.get(lot.lot_number) && lotCounts.get(lot.lot_number) > 1);
        this.blockFormLots.nativeElement.querySelector('#lotError' + index).style.display = isDuplicate ? 'block' : 'none';
      });
    }
  }

  showConfirm(index: number) {
    const popconfirm = this.popconfirms.toArray()[index];
    if (popconfirm) {
      popconfirm.show();
    }
  }

  handleClick() {
    this.clickButton.emit();
    this.formLots.reset();
    this.lots.clear();
    this.initAddLotsForm(this.data);
  }
}
