  constructor(
  ) { }



  /**
   * Lấy danh sách quận huyện theo mã tỉnh thành
   * @param provinceCode mã tỉnh thành
   * @returns danh sách quận huyện hoặc mảng rỗng nếu không tìm thấy
   */
  getDistricts(provinceCode: number): any {
    const province = this.provinces.find(province => province.code === provinceCode);
    return province ? province.districts : [];
  }



  /**
   * Lấy danh sách tỉnh thành không chứa thông tin quận huyện
   * @returns danh sách tỉnh thành không chứa thông tin quận huyện
   */
  getProvinces(): any {
    return this.provinces.map(province => {
      const { districts, ...provinceWithoutDistricts } = province;
      return provinceWithoutDistricts;
    });
  }

