import { Component, ElementRef, EventEmitter, Input, Output, QueryList, ViewChild, ViewChildren } from '@angular/core';
import { FormArray, FormBuilder, FormControl, FormGroup, Validators } from '@angular/forms';
import { VhAlgorithm, VhQuerySales } from 'ionic-vhframeworks';
import { NzPopconfirmDirective } from 'ng-zorro-antd/popconfirm';
import { VhComponent } from 'src/app/sales/components/vh-component/vh-component';
import { CleaveJsService } from 'src/app/sales/services/cleave-js.service';
import { LanguageService } from 'src/app/sales/services/language.service';
import { ProductService } from 'src/app/sales/services/product.service';

@Component({
  selector: 'app-modal-units-list',
  templateUrl: './modal-units-list.component.html',
  styleUrls: ['./modal-units-list.component.scss']
})
export class ModalUnitsListComponent {
  @ViewChildren(NzPopconfirmDirective) popconfirms!: QueryList<NzPopconfirmDirective>;
  @ViewChild('blockFormUnits') blockFormUnits: ElementRef;
  /** Có hiển thị hay không */
  @Input() isShowUnits: boolean = false;
  /** Danh sách barcode */
  @Input() barcodes: any = [];
  /** Đơn vị mặc định */
  @Input() defaultUnit: any;
  /** Danh sách đơn vị */
  @Input() listUnits: any[];
  /** Output khi đóng drawer */
  @Output() close = new EventEmitter<any>();
  @Output() clickButton = new EventEmitter<any>();

  /** Form group của danh sách lô */
  formUnits = this.formBuilderfb.group({
    units: this.formBuilderfb.array([], Validators.required),
  });
  /** Tỷ lệ mặc định */
  defaultRatio = 1;
  /**Error khi tỷ lệ quy đổi đã tồn tại */
  ratioError = true;

  management_product_price2: boolean = this.vhQuerySales.getLocalAppSettingNameBranch('permission_branch').management_product_price2;

  /** Mảng danh sách units. Mỗi unit bên trong là một form group */
  get units() {
    return this.formUnits.controls['units'] as FormArray;
  }

  handleClick() {
    this.formUnits.reset();
    this.units.clear();
    this.clickButton.emit();
    this.initAddUnitsForm(this.listUnits);
    this.defaultRatio = this.listUnits.find((e) => e.default)
      ? this.listUnits.find((e) => e.default).ratio
      : 1;
  }

  constructor(
    public vhAlgorithm: VhAlgorithm,
    private formBuilderfb: FormBuilder,
    private vhQuerySales: VhQuerySales,
    private vhComponent: VhComponent,
    private languageService: LanguageService,
    private productService: ProductService,
    private cleaveJsService: CleaveJsService,
  ) { }

  currentRatio: any;

  ngOnInit() {
    this.initAddUnitsForm(this.listUnits);
    this.defaultRatio = this.listUnits.find((e) => e.default)
      ? this.listUnits.find((e) => e.default).ratio
      : 1;
  }



  ngAfterViewInit() {
    // for (let i = 0; i < this.units.value.length; i++) {
    //   // đơn vị quy đổi
    //   if (this.units.value[i].ratio != 1) {
    //     this.vhAlgorithm.vhnumeral('#' + 'price input' + i);
    //     this.vhAlgorithm.vhnumeral('#' + 'price2 input' + i);
    //     this.vhAlgorithm.vhnumeral('#' + 'price_import input' + i);
    //   }
    // }
  }



  /**
   * Khởi tạo form danh sách units bằng cách thêm từng unit vào form
   * @param data
   */
  initAddUnitsForm(data) {
    if (data.length > 0) {
      for (let i = 0; i < data.length; i++) {
        this.addUnits(data[i]);
      }
    }
  }



  /**
   * Đóng drawer sẽ emit output close
   */
  onClose(success?) {
    this.formUnits.reset();
    this.units.clear();
    this.close.emit(success ? true : false);
  }



  /**
   * Thêm unit và danh sách units
   * @param unit unit cần thêm
   */
  addUnits(unit?) {
    const unitForm = this.formBuilderfb.group({
      unit: new FormControl(
        unit ? unit.unit : '', Validators.compose([Validators.required])
      ),
      price: this.createFormControl(unit ? unit.price : ''),
      price_str: this.createFormControl(unit ? this.vhAlgorithm.vhcurrencyunit(unit.price) : ''),
      price2: this.createFormControl(unit ? unit.price2 : ''),
      price2_str: this.createFormControl(unit ? this.vhAlgorithm.vhcurrencyunit(unit.price2) : ''),
      price_import: this.createFormControl(unit ? unit.price_import : ''),
      price_import_str: this.createFormControl(unit ? this.vhAlgorithm.vhcurrencyunit(unit.price_import) : ''),
      barcode: new FormControl(unit ? unit.barcode : ''),
      ratio: new FormControl(unit ? unit.ratio : this.getMissingRatio(), [Validators.required]),
      default: new FormControl(unit ? unit.default : false, [Validators.required]),
    });
    this.units.push(unitForm);
  }



  /**
   * Tạo form control cho các loại giá
   * @param value
   * @returns trả về form control đã tạo
   */
  private createFormControl(value: any): FormControl {
    return new FormControl(
      value,
      Validators.compose([
        Validators.required,
        Validators.pattern(
          '(^[0?=[.]] |^[^.,-][,0-9]*)(([.](?=[0-9]){1}[0-9]{0,5})|)'
        ),
      ])
    );
  }



  /**
   * Xoá unit theo index
   * @param index index của unit cần xoá
   */
  deleteUnits(index: number) {
    this.units.removeAt(index);
  }



  /**
   * Khi nhấn nút Lưu
   */
  saveUnits() {
    if ((this.blockFormUnits.nativeElement.querySelector(`[id^='ratioError']`) as HTMLElement)?.style.display == 'block') {
      this.vhComponent.alertMessageDesktop(
        'error',
        this.languageService.translate('Ratio already exists')
      );
    }
    else if ((this.blockFormUnits.nativeElement.querySelector(`[id^='unitError']`) as HTMLElement)?.style.display == 'block') {
      this.vhComponent.alertMessageDesktop(
        'error',
        this.languageService.translate('Unit already exists')
      );
    }
    else if ((this.blockFormUnits.nativeElement.querySelector(`[id^='barcodeError']`) as HTMLElement)?.style.display == 'block') {
      this.vhComponent.alertMessageDesktop(
        'error',
        this.languageService.translate('Barcode already exists')
      );
    }
    else {
      this.listUnits = this.units.value
        .filter(
          (item) =>
            item.unit !== '' &&
            typeof item.price === 'number' &&
            typeof item.price2 === 'number' &&
            typeof item.price_import === 'number' &&
            item.ratio
        )
        .map((item) => {
          // Create a new object without the unwanted properties
          const { price_str, price2_str, price_import_str, ...rest } = item;
          return rest;
        });
      this.close.emit(this.listUnits);
      this.formUnits.reset();
      this.units.clear();
    }
  }



  /**
   * Tạo barcode tự động tại vị trí position trong mảng
   * @param position
   */
  setBarcodeForUnit(position) {
    let newbarcode = this.productService.generateBarcode();
    let detailproduct = this.vhQuerySales.getlocalDetailProduct_byBarcode(newbarcode);
    if (detailproduct == null) {
      (this.units.at(position) as FormGroup).get('barcode').setValue(newbarcode);
      this.blockFormUnits.nativeElement.querySelector('#barcodeError' + position).style.display = 'none';
    }
    else {
      this.blockFormUnits.nativeElement.querySelector('$barcodeError' + position).style.display = 'block';
    }
  }



  /**
   * Khi thay đổi Giá
   * @param nameClass
   * @param position
   */
  changePrice(nameClass, position) {
    const value = this.cleaveJsService.getRawValue('#' + nameClass + position + ' input');
    (this.units.at(position) as FormGroup).get(nameClass).setValue(parseFloat(value ? value : '0'));
    this.cleaveJsService.destroyCleave('#' + nameClass + position + ' input');
  }


  /**
   * Khi thay đổi unit mặc định
   */
  changeUnitDefault() {
    this.units.value.forEach((unit) => {
      if (unit.ratio == this.defaultRatio) unit.default = true;
      else unit.default = false;
    });
  }



  /**
   * Khi thay đổi tỷ lệ quy đổi
   * @param ratio
   * @param position
   */
  checkRatioUnitExist(ratio, position) {
    if (ratio !== undefined) {
      const ratioCounts = new Map<string, number>();
      // Đếm số lần xuất hiện của mỗi ratio
      this.units.value?.forEach(unit => {
        const count = ratioCounts.get(unit.ratio) || 0;
        ratioCounts.set(unit.ratio, count + 1);
      });
      // Nếu số lần xuất hiện của ratio > 1 thì hiển thị lỗi
      this.units.value?.forEach((unit, index) => {
        const isDuplicate = unit.ratio === 1 || (ratioCounts.get(unit.ratio) && ratioCounts.get(unit.ratio) > 1);
        this.blockFormUnits.nativeElement.querySelector('#ratioError' + index).style.display = isDuplicate ? 'block' : 'none';
      });
    }
  }



  /**
   * Kiểm tra barcode đã tồn tại hay chưa
   * @param barcode barcode cần kiểm tra
   * @param position
   *
   */
  checkBarcodeExist(barcode, position) {
    if (barcode.length) {
      const barcodeCounts = new Map<string, number>();
      // Đếm số lần xuất hiện của mỗi barcode
      this.units.value?.forEach(unit => {
        const count = barcodeCounts.get(unit.barcode) || 0;
        barcodeCounts.set(unit.barcode, count + 1);
      });
      // Nếu số lần xuất hiện của barcode > 1 thì hiển thị lỗi
      this.units.value?.forEach((unit, index) => {
        const isDuplicate = this.vhQuerySales.getlocalDetailProduct_byBarcode(unit.barcode) || (barcodeCounts.get(unit.barcode) && barcodeCounts.get(unit.barcode) > 1);
        this.blockFormUnits.nativeElement.querySelector('#barcodeError' + index).style.display = isDuplicate ? 'block' : 'none';
      });
    }
    else {
      this.blockFormUnits.nativeElement.querySelector('#barcodeError' + position).style.display = 'none';
    }
  }



  /**
   * Kiểm tra Tên unit đã tồn tại hay chưa
   * @param unit tên unit cần check
   * @param position vị trí trong mảng
   */
  checkNameUnitExist(unit: string, position: number) {
    if (unit.length) {
      const unitCounts = new Map<string, number>();
      // Đếm số lần xuất hiện của mỗi unit
      this.units.value?.forEach(unit => {
        const count = unitCounts.get(unit.unit) || 0;
        unitCounts.set(unit.unit, count + 1);
      });
      // Nếu số lần xuất hiện của unit > 1 hoặc bằng với default thì hiển thị lỗi
      this.units.value?.forEach((unit, index) => {
        const isDuplicate = unit.unit == this.defaultUnit.unit || (unitCounts.get(unit.unit) && unitCounts.get(unit.unit) > 1);
        this.blockFormUnits.nativeElement.querySelector('#unitError' + index).style.display = isDuplicate ? 'block' : 'none';
      });
    }
  }

  showConfirm(index: number) {
    const popconfirm = this.popconfirms.toArray()[index];
    if (popconfirm) {
      popconfirm.show();
    }
  }

  getMissingRatio(): number {
    if (!this.units || this.units.length === 0) return 2;

    const ratios = Array.from(
      new Set(
        this.units.value
          .map((unit: any) => Math.floor(unit.ratio))
          .filter((ratio: number) => ratio >= 2)
      )
    ).sort((a: number, b: number) => a - b);

    let expected = 2;

    for (const ratio of ratios) {
      if (ratio !== expected) return expected;
      expected++;
    }

    return expected;
  }
}
