import { Injectable } from '@angular/core';
import { LanguageService } from './language.service';
import { VhQuerySales } from 'ionic-vhframeworks';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { FunctionService } from './function.service';


@Injectable({
  providedIn: 'root'
})
export class CustomerService {


  constructor(
    private languageService: LanguageService,
    private vhQuerySales: VhQuerySales,
    private functionService: FunctionService,
  ) {}



  /**
   * Trả về tên của khách hàng
   * @param id_customer id_customer cần lấy tên
   * @example this.renderPartner('JHIDUWHUYDWIUHUDY')
   * @returns string : customer_name | "Not found"
   */
  getCustomerName(id_customer: string) {
    let customer: string = this.languageService.translate(this.vhQuerySales.getlocalCustomer(id_customer).name);
    return customer ? customer : this.languageService.translate("Not found");
  }



  /**
   * Lấy danh sách khách hàng, chỉ lấy type != 3 và id != 'id_retail'
   * @returns Danh sách khách hàng
   */
  getCustomerList(): any {
    return this.vhQuerySales.getlocalCustomers().filter(item => item.type != 3).filter(item => item._id != 'id_retail');
  }



  /**
   * Khởi tạo form thêm khách hàng
   * @returns From thêm khách hàng
   */
  initAddCustomerForm(): FormGroup {
    return new FormGroup({
      img: new FormControl(null),
      name: new FormControl(null, Validators.compose([Validators.required])),
      email: new FormControl(null, Validators.compose([Validators.email])),
      gender: new FormControl(null),
      datebirth: new FormControl(null),
      phone: new FormControl(null, [
        Validators.required,
        this.functionService.phoneValidator(() => this.functionService.defaultCountryCode),
      ]),
      address: new FormControl(null),
      country: new FormControl(null),
      province: new FormControl(null),
      district: new FormControl(null),
      note: new FormControl(null),
      debt_enable: new FormControl(true),
      id_customer_class: new FormControl(null),
      type: new FormControl(1),
      id_branch: new FormControl(this.vhQuerySales.getDefaultBranch()._id),
      time_for_payment: new FormControl(0, Validators.compose([Validators.required])),
      delete_hidden: new FormControl(false),
      isBusiness: new FormControl(false),
      taxcode: new FormControl(null),
    });
  }

}
