import { ChangeDetectorRef, Component, EventEmitter, Output, ViewContainerRef } from '@angular/core';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { TranslateService } from '@ngx-translate/core';
import { VhAlgorithm, VhAuth, VhImage, VhQuery, VhQuerySales, VhEinvoice } from 'ionic-vhframeworks';
import { LanguageService } from 'src/app/sales/services/language.service';
import { VhComponent } from 'src/app/sales/components/vh-component/vh-component';
import { NzModalService } from 'ng-zorro-antd/modal';
import { ProductService } from 'src/app/sales/services/product.service';
import { CategoryService } from 'src/app/sales/services/category.service';
import { CleaveJsService } from 'src/app/sales/services/cleave-js.service';
import { SalesRetailSharedService } from '../../../sales-retail-shared.service';
import { NzMessageService } from 'ng-zorro-antd/message';
import { Platform } from '@ionic/angular';
import { BranchService } from 'src/app/sales/services/branch.service';
import { ThemeService } from 'src/app/sales/services/theme.service';
import { UserService } from 'src/app/sales/services/user.service';
import { BillService } from 'src/app/sales/services/bill.service';
import { CustomerService } from 'src/app/sales/services/customer.service';
import { PrintService } from 'src/app/sales/services/print.service';
import { SalesRetailUI } from '../../../sales-retail';
import { FunctionService } from 'src/app/sales/services/function.service';
import { VhType } from 'src/app/sales/interface/vh-type';


@Component({
  selector: 'app-modal-add-product',
  templateUrl: './modal-add-product.component.html',
  styleUrls: ['./modal-add-product.component.scss', './modal-sub-product.component.scss']
})
export class ModalAddProductComponent extends SalesRetailUI {

  /** Khi đóng */
  /** Khi đóng */
  @Output() onClose = new EventEmitter<any>();

  management_product_manysize: boolean = this.vhQuerySales.getLocalAppSettingNameBranch('permission_branch').management_product_manysize;
  management_product_lot: boolean = this.vhQuerySales.getLocalAppSettingNameBranch('permission_branch').management_product_lot;
  management_product_tax: boolean = this.vhQuerySales.getLocalAppSettingNameBranch('permission_branch').management_product_tax;
  management_product_price2: boolean = this.vhQuerySales.getLocalAppSettingNameBranch('permission_branch').management_product_price2;

  /** From Thêm sản phẩm */
  productAddForm: FormGroup;
  /** Danh sách danh mục loại 1 (sản phẩm) */
  listCategoriesAddProduct: any;
  /** Barcode chưa tồn tại hay rồi, true là chưa và false là rồi */
  isBarcodeNonExisting: boolean = true;
  /** Danh sách barcode */
  listBarcodes = [];

  /** Có hiển thị drawer danh sách Lô hay không */
  isShowLotsList = false;
  /** Danh sách lô hiển thị trong drawer Danh sách lô */
  listLots: any = [];

  /** Cho biết có tạo sản phẩm hay không, để khi destroy check để xoá ảnh nếu không tạo */
  isCreatedProduct: boolean = false;

  /** Giá bán lẻ - from add */
  price: any;
  /** Giá bán buôn / bán sỉ - form add */
  price2: any;
  /** Giá nhập - form add */
  price_import: any;

  /** Giá bán lẻ - from subproduct */
  price_subproduct: any;
  /** Giá bán buôn / bán sỉ - form subproduct */
  price2_subproduct: any;
  /** Giá nhập - form subproduct */
  price_subproduct_import: any;

  /** Danh sách subproduct */
  subProducts = [];
  /** Có hiển thị modal Thêm/Sửa subproduct hay không */
  isShowSubProduct = false;

  /** Có hiện drawer danh sách unit hay không */
  isShowUnits = false;

  /** Danh sách đơn vị */
  listUnits: any[] = [];
  /** Đơn vị mặc định */
  defaultUnit = {};

  /** From subproduct */
  formSubProduct: FormGroup;
  /** Thêm hay sửa subproduct. true là thêm, false là sửa */
  isSubproductAdd = true;
  /** index của subproduct cần update */
  indexSubUpdate: number = 0;

  constructor(
    protected salesSapoSharedService: SalesRetailSharedService,
    protected vhImage: VhImage,
    protected vhComponent: VhComponent,
    public languageService: LanguageService,
    public vhAuth: VhAuth,
    public vhQuerySales: VhQuerySales,
    protected router: Router,
    protected mess: NzMessageService,
    protected functionService: FunctionService,
    protected nzModalService: NzModalService,
    protected plaform: Platform,
    public vhAlgorithm: VhAlgorithm,
    protected branchService: BranchService,
    public categoryService: CategoryService,
    protected productService: ProductService,
    public themeService: ThemeService,
    public userService: UserService,
    public billService: BillService,
    protected customerService: CustomerService,
    public cdr: ChangeDetectorRef,
    protected viewContainerRef: ViewContainerRef,
    protected vhQuery: VhQuery,
    protected printService: PrintService,
    protected cleaveJsService: CleaveJsService,
    protected vhEinvoice: VhEinvoice,
  ) {
    super(
      salesSapoSharedService, vhComponent, languageService, vhAuth, vhQuerySales, router, mess, functionService, nzModalService,
      plaform, vhAlgorithm, branchService, categoryService, productService, themeService, userService,
      billService, customerService, cdr, viewContainerRef, vhQuery, printService, cleaveJsService, vhEinvoice
    );
  }


  ngOnInit() {
    this.productAddForm = this.productService.initAddProductForm();
    this.listCategoriesAddProduct = this.categoryService.getCategoryList(VhType.PRODUCT).map((category) => {
      return { label: category.name, value: category._id };
    });
  }

  ngAfterViewChecked() {
    // this.cdf.detectChanges();
  }

  ngAfterViewInit() {
    // this.vhAlgorithm.waitingStack().then(() => {
    //   this.price = this.vhAlgorithm.vhnumeral(".retail_price input");
    //   this.price_import = this.vhAlgorithm.vhnumeral(".price_import input");
    //   this.price2 = this.vhAlgorithm.vhnumeral(".price2 input");
    // });
  }

  ngOnDestroy() {
    // xóa hỉnh ảnh đã tải lên trên firestore nếu ko lưu  mà back về để tránh làm rác DB
    if (!this.isCreatedProduct && this.productAddForm.value['img']) {
      this.vhImage.deleteThumbnail([this.productAddForm.value['img']]);
    }
    this.cleaveJsService.destroyCleave('.price input');
    this.cleaveJsService.destroyCleave('.price2 input');
    this.cleaveJsService.destroyCleave('.price_import input');
  }

  closeModal() {
    this.nzModalService.closeAll();
    this.onClose.emit();
  }

  gotoConfigProduct() {
    this.router.navigate(['/sales/dashboard/settings/configuration/products'], { state: { goback: true } });
  }

  /**
   * Thực hiện chuyển đổi giá trị của input thành dạng số có dấu ngan cách. VD: 1000000 -> 1,000,000
   * @param class_name class name của input cần transform
   */
  transformPrice(class_name: string) {
    this[class_name] = this.cleaveJsService.getRawValue("." + class_name + ' input') ? this.cleaveJsService.getRawValue("." + class_name + ' input') : 0;
  }

  /**
   * Mở drawer danh sách lô sp
   * @param lots danh sách lô sẽ hiện
   */
  openListLots(lots?) {
    this.listBarcodes = [];
    if (!this.productAddForm.value.manysize) {
      this.listBarcodes = [this.productAddForm.value.barcode].concat(this.listUnits.map(item => { return item.barcode; }));
    }
    else {
      for (let i in this.subProducts) {
        this.listBarcodes = this.listBarcodes.concat([...this.subProducts[i].units.map(e => { return e.barcode; }), this.subProducts[i].barcode, ...this.subProducts[i].lots.map(lot => { return lot.barcode; })]);
      }
      this.listBarcodes.push(this.formSubProduct.value.barcode, ...this.formSubProduct.value.units.map(item => { return item.barcode; }));
    }
    this.isShowLotsList = true;
    if (lots) this.listLots = lots;
  }

  /**
   * Khi đóng drawer danh sách lô
   */
  closeLotList(listLots) {
    this.isShowLotsList = false;
    if (listLots) {
      if (this.productAddForm.value.manysize) {
        this.formSubProduct.controls['lots'].setValue(listLots);
        this.listLots = listLots;
      }
      else {
        this.listLots = listLots;
      }
    }
  }

  /* --------------------------------------------------------------- PRODUCT -------------------------------------------------------------- */
  /**
   * Lưu sản phẩm vào database.
   * Nếu manysize = true thì có lưu kèm theo subproduct
   */
  saveProduct() {
    this.productAddForm.removeControl('subProducts');
    let product = this.cleanForm(this.productAddForm).value;

    // nếu có manysize thì phải format lại các trường trong subs để lưu
    if (product['manysize']) {
      delete product['item_code'];
      if (this.subProducts.length === 0) {
        this.vhComponent.showToast(1500, 'Vui lòng thêm phân loại hàng hoá', 'alert-toast');
        return;
      }
      else {
        this.subProducts = this.subProducts.map(item => {
          let sub_product = {
            name: item.name,
            warning_number: Number(item.warning_number),
            item_code: item.item_code,
            // allow_sell: item.allow_sell,
            delete_hidden: item.delete_hidden,
            selling: item.selling,
            units: [
              {
                unit: item.unit,
                ratio: 1,
                price: parseFloat(item.price),
                price2: this.management_product_price2 ? parseFloat(item.price2) : 0,
                price_import: parseFloat(item.price_import),
                barcode: item.barcode,
                default: item.units.filter(e => e.default).length ? false : true
              },
              ...item.units
            ],
            manylot: item.manylot,
          };
          if (item['manylot']) {
            sub_product['lots'] = item.lots;
            sub_product['days_import_warning'] = item.days_import_warning;
            sub_product['days_exp_warning'] = item.days_exp_warning;
          }
          return sub_product;
        });
      }
    }
    else {
      this.subProducts = [];
      // xử lý units của sp
      product.units = new Array();
      product.price = parseFloat(this.price ? this.price : '0');
      product.price_import = parseFloat(this.price_import ? this.price_import : '0');
      product.price2 = this.management_product_price2 ? parseFloat(this.price2 ? this.price2 : '0') : '0';
      product.units = [...this.listUnits];
      product.units = [
        {
          unit: product.unit,
          ratio: 1,
          price: parseFloat(product.price),
          price2: this.management_product_price2 ? parseFloat(product.price2) : 0,
          price_import: parseFloat(product.price_import),
          barcode: product.barcode,
          default: product.units.filter(e => e.default).length ? false : true
        },
        ...product.units,
      ];
      if (product.warning_number >= 0) product.warning_number = parseFloat(product.warning_number);
      else product.warning_number = 0;
      if (product['manylot']) {
        product['lots'] = this.listLots;
      }
    }

    // Xóa các trường ko cần thiết
    Object.keys(product).forEach((key) => {
      if (key == 'unit' || key == 'ratio' || key == 'price' || key == 'price2' || key == 'price_import' || key == 'barcode') {
        delete product[key];
      }
    });
    // kiểm tra barcode đã tồn tại hay chưa
    if (!this.isBarcodeNonExisting) {
      this.vhComponent.alertMessageDesktop("error", this.languageService.translate("Barcode already exists"));
    }
    else {

      // thêm sản phẩm vào db
      this.vhComponent.showLoading('').then(() => {
        this.vhQuerySales.addProductAndSubsProduct(product, this.subProducts).then(() => {
          this.vhComponent.hideLoading(0);
          this.vhComponent.showToast(1000, product.name + " " + (this.languageService.translate("has been added successfully")), 'success-toast');
          setTimeout(() => {
            this.isCreatedProduct = true;
            this.closeModal();
            this.changeTabMainSector(this.indexTabSearchProductMainSector);
          }, 300);
        }, (err) => {
          console.log(err);
          this.vhComponent.hideLoading(0);
          this.vhComponent.showToast(1500, this.languageService.translate("An error occurred while adding product. Please try again"), 'alert-toast');
        });
      });
    }
  }



  /**
   * Hàm thực hiện xóa space (TRIM) các control trong form cho các trường name, barcode, brand, unit
   * @param formGroup
   * @returns formGroup đã được clean
   */
  cleanForm(formGroup: FormGroup): FormGroup {
    Object.keys(formGroup.controls).forEach((key) => {
      if (['name', 'barcode', 'brand', 'unit'].includes(key) && formGroup.get(key).value) {
        let value = formGroup.get(key).value.toString().trim();
        formGroup.get(key).setValue(value);
      }
    });
    return formGroup;
  }



  /**
   * Click vô input có id=file-upload để mở giao diện chọn file để upload ảnh.
   */
  getFile() {
    document.getElementById("file-upload").click();
  }



  /**
   * Lấy hình ảnh từ Desktop
   */
  onUpload(e?) {
    const file = e.target.files[0];
    this.vhImage.getThumbnailFromDesktop(file, 256, 256, "products", this.productAddForm.value['img'] || '').then(
      photoURL => {
        this.productAddForm.controls["img"].setValue(photoURL);
        this.vhComponent.showToast(2000, (this.languageService.translate("Image was loaded successfully")), 'success-toast');
      },
      () => {
        this.vhComponent.showToast(2000, (this.languageService.translate("Image was failed")), 'alert-toast');
      }
    );
  }



  /**
   * Tạo barcode tự động cho sản phẩm và thêm vào form control `barcode`
   */
  generateBarcode() {
    let newbarcode = this.productService.generateBarcode();
    this.checkBarcodeExisting(newbarcode);
    if (this.isBarcodeNonExisting) {
      this.productAddForm.controls['barcode'].setValue(newbarcode);
    }
  }



  /**
   * Kiểm tra barcode đã tồn tại hay chưa
   * @example this.checkBarcode('123456');
   */
  checkBarcodeExisting(barcode: string) {
    if (barcode) {
      let detailproduct = this.vhQuerySales.getlocalDetailProduct_byBarcode(barcode);
      if (detailproduct == null) {
        this.isBarcodeNonExisting = !(this.listUnits.find(e => e.barcode == barcode) || this.listLots.find(lot => lot.barcode == barcode));
      } else {
        this.isBarcodeNonExisting = false;
      }
    } else {
      this.isBarcodeNonExisting = true;
    }
  }



  /**
   * Khi đóng drawer thêm/danh sách đơn vị
   * @param event
   */
  closeUnitsList(event) {
    if (typeof event != 'boolean') {
      if (!this.productAddForm.value.manysize) {
        this.listUnits = event;
      }
      else {
        this.formSubProduct.controls['units'].setValue(event);
      }
    }
    this.isShowUnits = false;
  }



  /**
   * Mở drawer thêm/danh sách đơn vị
   */
  changeUnits() {
    this.listBarcodes = [];
    // xử lý gán danh sách barcode để kiểm tra trùng trong local
    if (!this.productAddForm.value.manysize) {
      this.listBarcodes = [this.productAddForm.value.barcode].concat(this.listUnits.map(item => { return item.barcode; })).concat(this.listLots.map(lot => { return lot.barcode; }));
    }
    this.isShowUnits = true;
    this.defaultUnit = {
      unit: this.productAddForm.value.unit,
      ratio: 1,
      barcode: this.productAddForm.value.barcode,
      price: parseFloat(this.price ? this.price : '0'),
      price2: this.management_product_price2 ? parseFloat(this.price2 ? this.price2 : '0') : 0,
      price_import: parseFloat(this.price_import ? this.price_import : '0')
    };
  }



  /**
   * Khi thay đổi tích chọn Phân loại hàng hóa (manysize)
   * @param e
   */
  onChangeManysize(e) {
    if (!e) {
      this.subProducts = [];
      this.productAddForm = this.productService.initAddProductForm(this.productAddForm.value);
      this.productAddForm.removeControl('subProducts');
    }
    else {
      this.productAddForm.addControl('subProducts', new FormControl('', [Validators.required]));
      this.productAddForm.removeControl('price');
      this.productAddForm.removeControl('price2');
      this.productAddForm.removeControl('price_import');
      this.productAddForm.removeControl('barcode');
      this.productAddForm.removeControl('warning_number');
      this.productAddForm.removeControl('unit');
      this.productAddForm.removeControl('selling');
      // this.productAddForm.removeControl('allow_sell');
      this.productAddForm.removeControl('manylot');
    }
  }



  /**
   * Khi thay đổi tích chọn Lô, hạn sử dụng (manylot)
   * @param event
   * @example this.onChangeManyLot(true)
   */
  onChangeManyLot(event) {
    if (event) {
      this.productAddForm.addControl('days_import_warning', new FormControl(null, [Validators.required]));
      this.productAddForm.addControl('days_exp_warning', new FormControl(null, [Validators.required]));
    }
    else {
      this.productAddForm.removeControl('days_import_warning');
      this.productAddForm.removeControl('days_exp_warning');
    }
  }



  /* ------------------------------------------------------------- SUBPRODUCTS ------------------------------------------------------------- */



  /**
   * Xoá subproduct khỏi danh sách
   * @param data
   * @example this.deleteSize(subproduct_selected);
   */
  deleteSize(data) {
    this.subProducts = this.subProducts.filter(sub => sub.name != data.name);
    if (this.subProducts.length == 0) {
      this.productAddForm.controls['subProducts'].setValue([]);
    } else {
      this.productAddForm.controls['subProducts'].setValue(this.subProducts);
    }
  }



  /**
   * Khi nhấn Lưu trong modal subproduct
   * @param value
   */
  handleFormSubProduct(value) {
    if (!this.isBarcodeNonExisting) {
      this.vhComponent.alertMessageDesktop("error", this.languageService.translate("Barcode already exists"));
    }
    else {
      let data = {
        ...value,
        name: value.name.toString().trim(),
        item_code: value.item_code.toString().trim(),
        barcode: value.barcode.toString().trim(),
        unit: value.unit.toString().trim(),
        units: value.units,
        lots: value.lots,
        price: this.price_subproduct ? parseFloat(this.price_subproduct) : 0,
        price2: this.management_product_price2 && this.price2_subproduct ? parseFloat(this.price2_subproduct) : 0,
        price_import: this.price_subproduct_import ? parseFloat(this.price_subproduct_import) : 0
      };
      if (!this.isSubproductAdd) {
        this.subProducts[this.indexSubUpdate] = data;
      }
      else {
        this.subProducts.push(data);;
      }
      this.productAddForm.controls['subProducts'].setValue(this.subProducts);
      this.isShowSubProduct = false;
      this.isEditSubProduct = false;
      this.cleaveJsService.destroyCleave('.price_subproduct input');
      this.cleaveJsService.destroyCleave('.price2_subproduct input');
      this.cleaveJsService.destroyCleave('.price_subproduct_import input');
    }

  }

  isEditSubProduct = false; // Biến kiểm tra có đang chỉnh sửa sub product

  /**
   * Hiển thị form thêm/sửa subproduct
   * @param data nếu có thì là form sửa, không có là form thêm
   */
  showSubProduct(data?) {
    this.isShowSubProduct = true;
    // this.vhAlgorithm.waitingStack().then(() => {
    //   this.price_subproduct = this.vhAlgorithm.vhnumeral(".price_subproduct input") || 0;
    //   this.price2_subproduct = this.vhAlgorithm.vhnumeral(".price2_subproduct input") || 0;
    //   this.price_subproduct_import = this.vhAlgorithm.vhnumeral(".price_subproduct_import input") || 0;
    // });
    this.listBarcodes = this.productService.getSubProductBarcodes(this.subProducts);
    if (data) {
      // Sửa
      this.formSubProduct = this.productService.initFormSubProduct(data);
      this.isSubproductAdd = false;
      this.isEditSubProduct = true;
      this.indexSubUpdate = this.subProducts.findIndex(item => item.name == data.name);
    }
    else {
      // Thêm
      this.formSubProduct = this.productService.initFormSubProduct();
      this.isSubproductAdd = true;
      this.isEditSubProduct = false;
    }
  }



  /**
   * tạo barcode tự động cho subproduct
   * @example this.generateBarcodeSubProduct()
   */
  generateBarcodeSubProduct() {
    let newbarcode = this.productService.generateBarcode();
    this.checkBarcodeSubProductExisting(newbarcode);
    if (this.isBarcodeNonExisting) {
      this.formSubProduct.controls['barcode'].setValue(newbarcode);
    }
  }



  /**
   * kiểm tra barcode của sub đã tồn tại hay chưa
   * @example this.checkBarcode('123456')
   */
  checkBarcodeSubProductExisting(barcode: string) {
    let detailproduct = this.vhQuerySales.getlocalDetailProduct_byBarcode(barcode);
    if (detailproduct == null) {
      this.isBarcodeNonExisting = !this.listBarcodes.includes(barcode);
    } else {
      this.isBarcodeNonExisting = false;
    }
  }



  /**
   * Mở drawer thêm/danh sách đơn vị của subproduct
   */
  changeUnitsSubs() {
    this.listBarcodes = this.productService.getSubProductBarcodes(this.subProducts);
    this.listBarcodes.push(this.formSubProduct.value.barcode, ...this.formSubProduct.value.units.map(item => { return item.barcode; }));
    this.isShowUnits = true;
    this.listUnits = this.formSubProduct.value.units;
    this.defaultUnit = {
      unit: this.formSubProduct.value.unit,
      ratio: 1,
      barcode: this.formSubProduct.value.barcode,
      price: this.price_subproduct ? parseFloat(this.price_subproduct) : 0,
      price2: this.management_product_price2 && this.price2_subproduct ? parseFloat(this.price2_subproduct) : 0,
      price_import: this.price_subproduct_import ? parseFloat(this.price_subproduct_import) : 0
    };
  }



  /**
    * Khi thay đổi tích chọn Lô, hạn sử dụng (manylot) của subproduct
    * @param event
    * @example this.onChangeManyLot(true)
    */
  onChangeManyLotSub(event) {
    if (event) {
      if (this.formSubProduct.get('days_import_warning') && this.formSubProduct.get('days_exp_warning')) {
        this.formSubProduct.get('days_import_warning')?.setValidators([Validators.required]);
        this.formSubProduct.get('days_exp_warning')?.setValidators([Validators.required]);
      }
      else {
        this.formSubProduct.addControl('days_import_warning', new FormControl(null, [Validators.required]));
        this.formSubProduct.addControl('days_exp_warning', new FormControl(null, [Validators.required]));
      }
      this.cdr.detectChanges();
    }
    else {
      this.formSubProduct.removeControl('days_import_warning');
      this.formSubProduct.removeControl('days_exp_warning');
    }
  }

  updateLotSubProduct(lots, index) {
    this.subProducts[index].lots = lots;
  }

  /**
     * Đóng modal Thuộc tính sản phẩm
     */
  closeSubProduct() {
    this.isShowSubProduct = false;
    this.isEditSubProduct = false;
    this.cleaveJsService.destroyCleave('.price_subproduct input');
    this.cleaveJsService.destroyCleave('.price2_subproduct input');
    this.cleaveJsService.destroyCleave('.price_subproduct_import input');
  }
}
