import { NzModalService } from 'ng-zorro-antd/modal';
import { FunctionService } from 'src/app/sales/services/function.service';
import { ChangeDetectorRef, Component, ElementRef, Renderer2, HostListener, ViewChild, ViewContainerRef } from '@angular/core';
import { VhComponent } from 'src/app/sales/components/vh-component/vh-component';
import { LanguageService } from 'src/app/sales/services/language.service';
import { VhAlgorithm, VhAuth, VhEinvoice, VhQuery, VhQuerySales } from 'ionic-vhframeworks';
import { Router } from '@angular/router';
import { NzMessageService } from 'ng-zorro-antd/message';
import { Platform } from '@ionic/angular';
import { BranchService } from '../services/branch.service';
import { CategoryService } from '../services/category.service';
import { ProductService } from '../services/product.service';
import { ThemeService } from '../services/theme.service';
import { UserService } from '../services/user.service';
import { BillService } from '../services/bill.service';
import { CustomerService } from '../services/customer.service';
import { PrintService } from '../services/print.service';
import { SalesRetailUI } from './sales-retail';
import { SalesRetailSharedService } from './sales-retail-shared.service';
import { NzPopconfirmDirective } from 'ng-zorro-antd/popconfirm';
import { CleaveJsService } from '../services/cleave-js.service';

@Component({
  selector: 'app-sales-retail',
  templateUrl: './sales-retail.component.html',
  styleUrls: [
    './sales-retail.component.scss',
    './sales-retail-custom.component.scss',
  ],
  providers: [SalesRetailSharedService]
})
export class SalesRetailComponent extends SalesRetailUI {

  @ViewChild('scrollableGrid') scrollableGrid: ElementRef;
  @ViewChild(NzPopconfirmDirective) popconfirm: NzPopconfirmDirective;

  constructor(
    protected salesSapoSharedService: SalesRetailSharedService,
    protected vhComponent: VhComponent,
    public languageService: LanguageService,
    public vhAuth: VhAuth,
    public renderer: Renderer2,
    public vhQuerySales: VhQuerySales,
    protected router: Router,
    protected mess: NzMessageService,
    protected functionService: FunctionService,
    protected nzModalService: NzModalService,
    protected plaform: Platform,
    public vhAlgorithm: VhAlgorithm,
    protected branchService: BranchService,
    public categoryService: CategoryService,
    protected productService: ProductService,
    public themeService: ThemeService,
    public userService: UserService,
    public billService: BillService,
    protected customerService: CustomerService,
    protected cdr: ChangeDetectorRef,
    protected viewContainerRef: ViewContainerRef,
    protected vhQuery: VhQuery,
    protected printService: PrintService,
    protected element: ElementRef,
    protected cleaveJsService: CleaveJsService,
    protected vhEinvoice: VhEinvoice,
  ) {
    super(
      salesSapoSharedService, vhComponent, languageService, vhAuth, vhQuerySales, router, mess, functionService, nzModalService,
      plaform, vhAlgorithm, branchService, categoryService, productService, themeService, userService,
      billService, customerService, cdr, viewContainerRef, vhQuery, printService, cleaveJsService, vhEinvoice
    );
  }


  ngOnInit() {
    this.onInit();
    this.adjustScale();
    window.addEventListener('resize', this.adjustScale.bind(this));

    this.addCustomerForm = this.customerService.initAddCustomerForm();
  }



  ngOnDestroy() {
    this.onDestroy();

    window.removeEventListener('resize', this.adjustScale.bind(this));
    if (this.observerScale) {
      this.observerScale.disconnect();
    }
    if (this.scrollableGridResizeObserver) {
      this.scrollableGridResizeObserver.disconnect();
    }
    document.body.style.zoom = ``;
  }

  observerScale = new ResizeObserver(() => {
    this.adjustScale();
  });

  // Hàm thay đổi zoom để full screen
  adjustScale() {
    const container = this.element.nativeElement.children[0];
    if (!container) return;
    const viewportHeight = window.innerHeight;
    const containerHeight = container.offsetHeight;
    const scaleY = viewportHeight / containerHeight;
    container.style.zoom = scaleY;
    if (container) {
      this.observerScale.observe(container);

    }
  }

  // Xử lý hiển thị nút di chuyển scroll khi danh sách bill overflow
  showPrev = false; // Hiện nút scroll left
  showNext = false; // Hiện nút scroll right
  private scrollableGridResizeObserver!: ResizeObserver;

  private observeResize() {
    this.scrollableGridResizeObserver = new ResizeObserver(() => this.checkScroll());
    this.scrollableGridResizeObserver.observe(this.scrollableGrid.nativeElement);
  }

  checkScroll() {
    const grid = this.scrollableGrid.nativeElement;
    if (!grid) return;

    const scrollLeft = grid.scrollLeft;
    const scrollWidth = grid.scrollWidth;
    const clientWidth = grid.clientWidth;

    const delta = 2;

    const atStart = scrollLeft <= delta;
    const atEnd = scrollLeft + clientWidth >= scrollWidth - delta;

    this.showPrev = !atStart;
    this.showNext = !atEnd;
  }

  scrollLeft() {
    this.scrollableGrid.nativeElement.scrollBy({ left: -200, behavior: 'smooth' });
    setTimeout(() => this.checkScroll(), 300);
  }

  scrollRight() {
    this.scrollableGrid.nativeElement.scrollBy({ left: 200, behavior: 'smooth' });
    setTimeout(() => this.checkScroll(), 300);
  }

  callbackAfterGetDataSelect() {
    setTimeout(() => {
      if (!this.scrollableGridResizeObserver) {
        this.observeResize();
      }
      this.checkScroll();

      const container = this.scrollableGrid.nativeElement as HTMLElement;

      container.addEventListener('wheel', (e: WheelEvent) => {
        if (e.deltaY !== 0) {
          container.scrollLeft += e.deltaY;
          this.checkScroll();
          e.preventDefault(); // Prevent vertical scroll
        }
      });
    }, 0);
  }


  // Xử lý hiển thị danh sách sản phẩm tìm kiếm
  isOpenListProductSearch = false;

  handleShowListProduct() {
    this.isOpenListProductSearch = true;
  }

  handleHideListProduct() {
    this.isOpenListProductSearch = false;
  }

  /* Hiển thị modal confirm xóa tất cả bill detail */
  showConfirm() {
    if (this.popconfirm) {
      this.popconfirm.show();
    }
  }



  /**
   * bắt sự kiện nhấn trên bàn phím
   */
  eventKeypress() {
    this.event = (event: any) => {
      const key = event.key;

      // F3 - Tìm kiếm sản phẩm
      if (key === 'F3') {
        event.preventDefault();
        (document.querySelector('.input-search-product-f3 input') as HTMLInputElement)?.focus();
      }
      // F9 - Thanh toán
      else if (key === 'F9') {
        event.preventDefault();
        let modalDrawer = document.querySelector('.btn-modal__drawer:has(.modal-payment-btn-complete-f9)') as HTMLElement;
        // Nếu modal Thanh toán đang hiển thị
        if (modalDrawer && modalDrawer.style.opacity !== '0') {
          (document.querySelector('.btn-modal__drawer .modal-payment-btn-complete-f9 button') as HTMLElement)?.click();
        }
        // Nếu không
        else {
          (document.querySelector('app-modal-payment .btn-modal .btn-modal__boundary') as HTMLElement)?.click();
        }
      }
      // Escape - ESC
      else if (key === 'Escape') {
        event.preventDefault();
        (document.querySelector('.btn-esc') as HTMLElement)?.click();
      }
      // F8 - OK / Hoàn tất / ...
      else if (key === 'F8') {
        event.preventDefault();
        (document.querySelector('.btn-f8') as HTMLElement)?.click();
      }
    };
    document.addEventListener('keydown', this.event, false);
  }

  gotoInvoices() {
    this.router.navigate(['/sales/sales-retail/invoices-pos']);
  }


  /* -------------------------------------------------------------------------------------------------------------------------------------- */
  /*                                                              CHỌN GIÁ BÁN                                                              */
  /* -------------------------------------------------------------------------------------------------------------------------------------- */

  /** Có mở modal Chọn giá bán hay không */
  isShowSelectPriceType: boolean = false;



  barcode: string = '';
  barcode_array = [];
  @HostListener("window: keypress", ["$event"])
  /**
   * Sự kiện nhận barcode
   */
  getKeyUp(_event: KeyboardEvent) {
    if (this.router.url === '/sales/sales-retail' && !this.showModalOtherDiscount && !this.isShowEditPrice) {
      if (_event.key == 'Enter') {
        this.barcode_array.forEach((i, index) => {
          if (i == 'Shift') {
            this.barcode_array[index + 1] = this.barcode_array[
              index + 1
            ].toUpperCase();
            this.barcode_array.splice(index, 1);
          }
        });

        this.barcode = this.barcode_array.join('');
        setTimeout(() => {
          if (this.barcode) {
            let detailProduct = this.vhQuerySales.getlocalDetailProduct_byBarcode(this.barcode);
            if (detailProduct) {
              this.chooseProduct({ ...detailProduct, ...this.vhQuerySales.getUnit_byRatio(detailProduct.units, detailProduct.ratio) });
            }
            else {
              if (this.barcode.length > 3) {
                this.vhComponent.alertMessageDesktop('error', this.languageService.translate('Not found'));
              }
            }
          }
          this.barcode_array = [];
        }, 100);
      }
      else {
        this.barcode_array.push(_event.key);
      }
    }
  }
}

