import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { NgZorroAntModule } from 'src/app/ng-zorro-ant.module';
import { TranslateModule } from '@ngx-translate/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { IonicModule } from '@ionic/angular';
import { RouterModule, Routes } from '@angular/router';
import { LottieModule } from 'ngx-lottie';
import { playerFactory } from 'src/app/app.module';
import { SalesRetailComponent } from './sales-retail.component';
import { ScrollingModule } from '@angular/cdk/scrolling';
import { EwaComponentsModule } from 'ewa-components-angular';
import { BillDetailItemComponent } from './components/bill-detail-item/bill-detail-item.component';
import { ModalPaymentComponent } from './components/modal/modal-payment/modal-payment.component';
import { ListProductSearchComponent } from './components/list-product-search/list-product-search.component';
import { DiscountComponent } from './components/modal/discount/discount.component';
import { SearchCustomerComponent } from './components/modal/search-customer/search-customer.component';
import { AddCustomerComponent } from './components/modal/add-customer/add-customer.component';
import { BillComponent } from './components/bill/bill.component';
import { OtherDiscountsComponent } from './components/modal/other-discounts/other-discounts.component';
import { SelectPriceTypeComponent } from './components/modal/select-price-type/select-price-type.component';
import { EditPriceComponent } from './components/modal/edit-price/edit-price.component';
import { SelectLotComponent } from './components/modal/select-lot/select-lot.component';
import { SelectLotInsideComboComponent } from './components/modal/select-lot-inside-combo/select-lot-inside-combo.component';
import { ModalAddProductComponent } from './components/modal/modal-add-product/modal-add-product.component';
import { ModalLotListComponent } from './components/modal/modal-lot-list/modal-lot-list.component';
import { ModalUnitsListComponent } from './components/modal/modal-units-list/modal-units-list.component';
import { SelectGiftComponent } from './components/modal/select-gift/select-gift.component';
import { ConfigPrintComponent } from './components/modal/config-print/config-print.component';
import { ConfigSalesComponent } from './components/modal/config-sales/config-sales.component';
import { AccountInformationComponent } from './components/modal/account-information/account-information.component';
import { ModalEditServiceTimeComponent } from './components/modal/modal-edit-service-time/modal-edit-service-time.component';
import { ModalEditUsedTimeComponent } from './components/modal/modal-edit-used-time/modal-edit-used-time.component';
import { CheckFeesComponent } from './components/check-fees/check-fees.component';
import { DeliveryPosComponent } from './components/delivery-pos/delivery-pos.component';

const routes: Routes = [
  {
    path: '',
    component: SalesRetailComponent,
  },
  {
    path: 'invoices-pos',
    loadChildren: () => import('./components/invoices-pos/invoices-pos.module').then(m => m.InvoicesPosModule)
  },
];

@NgModule({
  declarations: [
    SalesRetailComponent,
    ModalAddProductComponent,
    BillDetailItemComponent,
    ModalPaymentComponent,
    ListProductSearchComponent,
    DiscountComponent,
    SearchCustomerComponent,
    AddCustomerComponent,
    BillComponent,
    OtherDiscountsComponent,
    SelectPriceTypeComponent,
    EditPriceComponent,
    SelectLotComponent,
    SelectLotInsideComboComponent,
    ModalLotListComponent,
    ModalUnitsListComponent,
    SelectGiftComponent,
    ConfigPrintComponent,
    ConfigSalesComponent,
    AccountInformationComponent,
    ModalEditServiceTimeComponent,
    ModalEditUsedTimeComponent,
    DeliveryPosComponent,
    CheckFeesComponent,
  ],
  imports: [
    EwaComponentsModule,
    ScrollingModule,
    CommonModule,
    RouterModule.forChild(routes),
    NgZorroAntModule,
    IonicModule,
    ReactiveFormsModule,
    FormsModule,
    TranslateModule,
    LottieModule.forRoot({ player: playerFactory }),
  ]
})
export class SalesRetailModule { }
