import { ChangeDetectorRef, Component, Input, SimpleChanges, ViewContainerRef } from '@angular/core';
import { SalesRetailUI } from '../../../sales-retail';
import { VhComponent } from 'src/app/sales/components/vh-component/vh-component';
import { LanguageService } from 'src/app/sales/services/language.service';
import { VhAlgorithm, VhAuth, VhQuery, VhQuerySales,VhEinvoice } from 'ionic-vhframeworks';
import { FunctionService } from 'src/app/sales/services/function.service';
import { NzModalService } from 'ng-zorro-antd/modal';
import { Platform } from '@ionic/angular';
import { Router } from '@angular/router';
import { NzMessageService } from 'ng-zorro-antd/message';
import { CustomerService } from 'src/app/sales/services/customer.service';
import { BillService } from 'src/app/sales/services/bill.service';
import { UserService } from 'src/app/sales/services/user.service';
import { ThemeService } from 'src/app/sales/services/theme.service';
import { PrintService } from 'src/app/sales/services/print.service';
import { ProductService } from 'src/app/sales/services/product.service';
import { CategoryService } from 'src/app/sales/services/category.service';
import { BranchService } from 'src/app/sales/services/branch.service';
import { SalesRetailSharedService } from '../../../sales-retail-shared.service';
import { CleaveJsService } from 'src/app/sales/services/cleave-js.service';

interface Denomination {
  value: number;
  label: string;
}

@Component({
  selector: 'app-modal-payment',
  templateUrl: './modal-payment.component.html',
  styleUrls: [
    './modal-payment.component.scss',
    './modal-payment-custom.component.scss',
  ]
})
export class ModalPaymentComponent extends SalesRetailUI {
  @Input() totalAmount: number;
  /** Có in khi thanh toán không */
  @Input() printWhenPay: boolean;

  denominations: Denomination[] = [
    { value: 100000, label: '100,000₫' },
    { value: 50000, label: '50,000₫' },
    { value: 10000, label: '10,000₫' },
    { value: 5000, label: '5,000₫' },
  ];
  suggestedDenominations: number[] = [];

  

  constructor(
    protected salesSapoSharedService: SalesRetailSharedService,
    protected vhComponent: VhComponent,
    public languageService: LanguageService,
    public vhAuth: VhAuth,
    public vhQuerySales: VhQuerySales,
    protected router: Router,
    protected mess: NzMessageService,
    protected functionService: FunctionService,
    protected nzModalService: NzModalService,
    protected plaform: Platform,
    public vhAlgorithm: VhAlgorithm,
    protected branchService: BranchService,
    public categoryService: CategoryService,
    protected productService: ProductService,
    public themeService: ThemeService,
    public userService: UserService,
    public billService: BillService,
    protected customerService: CustomerService,
    protected cdr: ChangeDetectorRef,
    protected viewContainerRef: ViewContainerRef,
    protected vhQuery: VhQuery,
    protected printService: PrintService,
    protected cleaveJsService: CleaveJsService,
    protected vhEinvoice: VhEinvoice,
  ) {
    super(
      salesSapoSharedService, vhComponent, languageService, vhAuth, vhQuerySales, router, mess, functionService, nzModalService,
      plaform, vhAlgorithm, branchService, categoryService, productService, themeService, userService,
      billService, customerService, cdr, viewContainerRef, vhQuery, printService, cleaveJsService, vhEinvoice
    );
  }




  ngOnChanges(changes: SimpleChanges): void {
    if (changes.totalAmount) {
      this.suggestedDenominations = this.generateSuggestedDenominations(changes.totalAmount.currentValue).slice(0, 12);
    }
  }



  private generateSuggestedDenominations(total: number): number[] {
    const denominationValues = this.denominations.map(d => d.value);
    const subsetSums = this.getAllSubsetSums(denominationValues);
    const paymentAmounts = subsetSums.map(sum => total + sum);
    paymentAmounts.push(total); // Include the exact total
    const uniqueAmounts = Array.from(new Set(paymentAmounts))
      .filter(amount => amount >= total)
      .sort((a, b) => a - b);
    return uniqueAmounts;
  }



  private getAllSubsetSums(denominations: number[]): number[] {
    const sums = new Set<number>();
    const n = denominations.length;
    for (let i = 1; i < (1 << n); i++) { // Start from 1 to exclude empty subset
      let sum = 0;
      for (let j = 0; j < n; j++) {
        if (i & (1 << j)) {
          sum += denominations[j];
        }
      }
      sums.add(sum);
    }
    return Array.from(sums);
  }



  /**
   * Khi chọn Tiền mặt trong Danh sách tiền mặt gợi ý
   * @param amount Số tiền chọn 
   */
  selectCash(amount) {
    this.selectedBill.cash = amount;
    this.updateBill();
  }

}
