import { ChangeDetectorRef, Component, ElementRef, HostListener, ViewContainerRef } from '@angular/core';
import { VhComponent } from 'src/app/sales/components/vh-component/vh-component';
import { LanguageService } from 'src/app/sales/services/language.service';
import { VhAlgorithm, VhAuth, VhQuery, VhQuerySales,VhEinvoice } from 'ionic-vhframeworks';
import { Router } from '@angular/router';
import { NzMessageService } from 'ng-zorro-antd/message';
import { FunctionService } from 'src/app/sales/services/function.service';
import { NzModalService } from 'ng-zorro-antd/modal';
import { Platform } from '@ionic/angular';
import { BranchService } from 'src/app/sales/services/branch.service';
import { CategoryService } from 'src/app/sales/services/category.service';
import { ProductService } from 'src/app/sales/services/product.service';
import { ThemeService } from 'src/app/sales/services/theme.service';
import { UserService } from 'src/app/sales/services/user.service';
import { BillService } from 'src/app/sales/services/bill.service';
import { CustomerService } from 'src/app/sales/services/customer.service';
import { PrintService } from 'src/app/sales/services/print.service';
import { SalesRetailUI } from '../../sales-retail';
import { SalesRetailSharedService } from '../../sales-retail-shared.service';
import { CleaveJsService } from 'src/app/sales/services/cleave-js.service';

@Component({
  selector: 'app-bill',
  templateUrl: './bill.component.html',
  styleUrls: [
    './bill.component.scss',
    './bill-custom.component.scss',
  ]
})
export class BillComponent extends SalesRetailUI {

  /** Có in khi thanh toán không */
  printWhenPay: boolean = false;
  /** Danh sách Thuế theo dạng {label: any, value: any} để bỏ vào select */
  taxOptions: any;



  constructor(
    protected salesSapoSharedService: SalesRetailSharedService,
    protected vhComponent: VhComponent,
    public languageService: LanguageService,
    public vhAuth: VhAuth,
    public vhQuerySales: VhQuerySales,
    protected router: Router,
    protected mess: NzMessageService,
    protected functionService: FunctionService,
    protected nzModalService: NzModalService,
    protected plaform: Platform,
    public vhAlgorithm: VhAlgorithm,
    protected branchService: BranchService,
    public categoryService: CategoryService,
    public productService: ProductService,
    public themeService: ThemeService,
    public userService: UserService,
    public billService: BillService,
    public customerService: CustomerService,
    protected cdr: ChangeDetectorRef,
    protected viewContainerRef: ViewContainerRef,
    protected vhQuery: VhQuery,
    protected printService: PrintService,
    private element: ElementRef,
    protected cleaveJsService: CleaveJsService,
    protected vhEinvoice: VhEinvoice,
  ) {
    super(
      salesSapoSharedService, vhComponent, languageService, vhAuth, vhQuerySales, router, mess, functionService, nzModalService,
      plaform, vhAlgorithm, branchService, categoryService, productService, themeService, userService,
      billService, customerService, cdr, viewContainerRef, vhQuery, printService, cleaveJsService, vhEinvoice
    );
  }



  ngOnInit() {
    this.taxOptions = this.billService.listTax.map(item => ({ label: this.vhAlgorithm.vhpercent(item.value / 100), value: item.value }));
  }



  @HostListener('document:click', ['$event'])
  onClickOutside(event: Event) {
    if (this.isShowSearchCustomer) {
      const customerContainer = this.element.nativeElement.querySelector('.comp-67aef48d8a1ac1679b4dc480');
      if (customerContainer && !customerContainer.contains(event.target)) {
        this.isShowSearchCustomer = false;
      }
    }
  }



  /* -------------------------------------------------------------------------------------------------------------------------------------- */
  /*                                                             MODAL GIẢM GIÁ                                                             */
  /* -------------------------------------------------------------------------------------------------------------------------------------- */

  /** Có hiện modal Giảm giá (app-discount) hay không */
  isShowDiscountModal = false;

  /**
   * Nhận sự kiện onOk từ modal giảm giá
   * @param event Giá trị và loại giảm giá. Ví dụ: {value: 100, type: 'percent'}
   */
  onOkDiscount(event) {
    this.isShowDiscountModal = false;
    if (event.type === 'percent') {
      this.onChangePercentDiscount(event.value);
    }
    else {
      this.onChangeDiscount();
    }
    this.setTotal();
    this.updateBill();
  }



  /* -------------------------------------------------------------------------------------------------------------------------------------- */
  /*                                                               KHÁCH HÀNG                                                               */
  /* -------------------------------------------------------------------------------------------------------------------------------------- */

  /** Có mở modal tìm khách hàng hay không */
  isShowSearchCustomer: boolean = false;
  /** value search khách hàng */
  searchCustomerInput: string = '';
  /** Có mở modal thêm khách hàng hay không */
  isShowAddCustomer: boolean = false;



  /**
   * Lấy tên khách hàng từ _id trong customerList
   * @param customerId _id của khách hàng cần lấy tên
   * @returns Tên khách hàng
   */
  getCustomerName(customerId: string): string {
    if (!customerId) {
      return this.searchCustomerInput;
    }
    else {
      // Nếu là khách lẻ
      if (customerId === 'id_retail') {
        return !this.selectedBill.retail_name ? '' : (this.selectedBill.retail_name + ' - ' + this.selectedBill.retail_phone + ' - ' + this.selectedBill.retail_address);
      }
      // Nếu là Thành viên
      else {
        const customer = this.customerList.find(c => c._id === customerId);
        return customer ? customer.name : '';
      }
    }
  }



  /**
   * Khi click vào icon right của input search customer
   */
  handleRightContentClick() {
    if (this.selectedBill.id_customer && this.selectedBill.id_customer === 'id_retail') {
      // mở form thêm khách lẻ
      this.isShowSearchCustomer = false;
      this.isShowAddCustomer = true;
      this.addCustomerForm = this.customerService.initAddCustomerForm();
    }
    else if (this.selectedBill.id_customer) {
      this.selectedBill.id_customer = 'id_retail';
      this.searchCustomerInput = '';
    }
    this.isShowSearchCustomer = true;
  }



  /* -------------------------------------------------------------------------------------------------------------------------------------- */
  /*                                                              GIẢM GIÁ KHÁC                                                             */
  /* -------------------------------------------------------------------------------------------------------------------------------------- */

  /** Có mở modal Giảm giá khác hay không */
  isShowOtherDiscounts: boolean = false;

}
