import { Injectable } from '@angular/core';
import { VhAlgorithm, VhQuerySales } from 'ionic-vhframeworks';
import { VhType } from '../interface/vh-type';


@Injectable({
  providedIn: 'root'
})
export class CategoryService {

  searchProductTabMainSector: string[] = ['Product', 'Service', 'Combo'];


  constructor(
    private vhQuerySales: VhQuerySales,
    private vhAlgorithm: VhAlgorithm,
  ) {}



  /**
   * Lấy danh sách danh mục sản phẩm
   * @param type = 3 là danh mục sản phẩm, = 2 là danh mục dịch vụ, = 5 là combos, = 23 là sản phẩm và dịch vụ, giá trị khác là cả ba
   */
  getCategoryList(type) {
    const categories = this.vhQuerySales.getlocalCategorys(type).filter(e => e.type == type);
    return this.vhAlgorithm.sortStringbyASC(categories, 'name');
  }



  /**
   * Lọc sản phẩm theo danh mục
   * @param type = 3 là danh mục sản phẩm, = 2 là danh mục dịch vụ, = 5 là combos, = 23 là sản phẩm và dịch vụ, giá trị khác là cả ba
   * @param id_category_selected id danh mục được chọn, nếu là 'all' thì lấy tất cả sản phẩm
   * @param auto_hidden_lot tự động ẩn lô hết hạn
   * @param auto_select_lot tự động chọn lô
   * @param allProductsList danh sách tất cả sản phẩm cần lọc
   * @returns danh sách sản phẩm đã lọc
   */
  filterByCategory(type, id_category_selected, auto_hidden_lot, auto_select_lot, allProductsList: any): any {
    let products = this.vhQuerySales.getlocalDetailProducts(type).filter(e => !e.delete_hidden)
    if(id_category_selected != 'all') {
      products = products.filter(pro => pro.id_category == id_category_selected);
    }
    allProductsList = products.filter(e => !e.delete_hidden).map(item => {
      let unit = this.vhQuerySales.getUnit_byRatio(item.units, item.units.find(e => e.default) ? item.units.find(e => e.default).ratio : 1);
      if (item.type == VhType.COMBO) {
        this.handleLotsOfCombo(item.combos.filter(e => e.lots), auto_hidden_lot, auto_select_lot);
        item.subComboName = this.getAllName_fromCombos(item.combos);
      }
      if (item.type == VhType.PRODUCT && item.manylot) {
        item.lots = this.vhAlgorithm.sortDatebyASC(item.lots, 'date_exp');
        if (auto_hidden_lot) item.lots = item.lots.filter(e => new Date(e.date_exp).getTime() >= new Date().getTime());
        if (auto_select_lot && item.lots.length) {
          let min_date_exp = this.vhAlgorithm.sortDatebyASC(item.lots.filter(e => new Date(e.date_exp).getTime() >= new Date().getTime()), 'date_exp');
          if (min_date_exp.length) {
            item['id_lotproduct'] = min_date_exp[0]._id;
            item['lot_number'] = min_date_exp[0].lot_number;
          }
        }
      }
      return { ...item, ...unit };
    });
    return allProductsList;
  }



  /**
   * Tự động chọn lô cho sp trong combo để dễ xử lý logic khi bán hàng
   * @param combos 
   * @param auto_hidden_lot Tự động ẩn lô khi hết hạn sử dụng
   * @param auto_select_lot Tự động chọn lô có HSD gần nhất khi bán hàng
   * @example this.handleLotsOfCombo(combos)
   */
  handleLotsOfCombo(combos, auto_hidden_lot, auto_select_lot) {
    combos = this.vhAlgorithm.sortDatebyASC(combos, 'date_exp');
    for (let i of combos) {
      if (i.lots.length) {
        if (auto_hidden_lot) {
          i.lots = i.lots.filter(e => new Date(e.date_exp).getTime() >= new Date().getTime());
        }
        if(i.lots.length && auto_select_lot) {
          let min_date_exp = this.vhAlgorithm.sortDatebyASC(i.lots, 'date_exp');
          i['id_lotproduct'] = min_date_exp[0]._id;
        }
      }
    }
  }



  /**
   * Trả về tên chuỗi sản phẩm trong combos
   * @param combos 
   * @returns string
   * @example let comboName = this.getAllName_fromCombos([]);
   */
  getAllName_fromCombos(combos: any) {
    return combos.map(e => { return e.quantity + 'x ' + e.name }).join(', ');
  }

}
