  onInit() {
    if (!this.vhAuth.localStorageGET("default_discount_price_type")) {
      this.vhAuth.localStorageSET("default_discount_price_type", { value: '%' });
    }
    // if (this.syncStatus) {
    this.useBranch = this.branchService.checkUsingBranch();
    let splash = document.getElementById("lottie-sync");
    if (splash) splash.remove();
    // this.removeSync();

    this.getBillsToday();
    this.checkBranches();
    this.employee = this.vhAuth.getlocalEmployee(this.vhAuth.getUser()._id);

    // check xem nv bán mặc định giá nào
    if (this.vhAuth.checkMyPermission('default_price_type_1')) this.price_type = 1;
    else if (this.vhAuth.checkMyPermission('default_price_type_2')) this.price_type = 2;

    // Kiểm tra tài khoản hết hạn chưa
    this.userService.checkExpireAccount();

    if (this.plaform.is('electron')) {
      this.vhQuerySales.connectDisplayLEDScreens();
    }

    this.eventKeypress();

    // best selling
    this.initProductsSelling();
    this.initServicesSelling();

    // get các loại chương trình tích điểm
    Promise.all([
      this.vhQuerySales.getEarningPointsBills(),
      this.vhQuerySales.getEarningPointsProducts(),
    ]).then(([earning_point_bills, earning_points_products,]: any) => {
      this.listEarningPointBills = earning_point_bills;
      this.listEarningPointsProducts = earning_points_products;
      this.listCustomerGroups = this.vhQuerySales.getlocalCustomerClasss();
    });

    this.sales_enable_edit_create_date = this.vhAuth.getUser().owner == 'boss' ? true : this.vhAuth.checkLocalMyPermissionName("sales_enable_edit_create_date");

    // Lấy dữ liệu phần in khi thanh toán
    this.observablePrinter = this.vhQuerySales.getObservableLocalPrinters().subscribe((localPrinters: any) => {
      let printer = this.vhQuerySales.getLocalPrintPage('page_desktop_sales');
      if (printer) this.checkPrinter = printer.print_sizes[printer.print_sizes.findIndex(e => e._id == printer.print_size_default)].enable;
      else this.checkPrinter = false;
    });
  }



  onDestroy() {
    document.removeEventListener('keydown', this.event, false);
    this.vhQuerySales.closeSyncOpeningBill();
    if (this.plaform.is('electron')) this.vhQuerySales.disconnectDisplayLEDScreens();
    this.observablePrinter?.unsubscribe();
    this.cleaveJsService.destroyAllCleave();
  }



