  /**
   * Lấy dữ liệu Hạng khách hàng, Điểm và Chương trình tích điểm (hoá đơn / sản phẩm) của khách hàng được chọn.
   * Kiểm tra xem có phải hoá đơn được giảm giá hay không
   */
  getClassAndProgramFirstLoad(id_customer) {
    this.selectedCustomerClass = this.listCustomerGroups.find(item => item._id == this.vhQuerySales.getlocalCustomer(id_customer).id_customer_class);

    if (this.selectedCustomerClass) {
      this.selectedCustomerProgramBill = this.listEarningPointBills.find(item => item._id === this.selectedCustomerClass.id_earning_points_bill) || null;
      this.selectedCustomerProgramProduct = this.listEarningPointsProducts.find(item => item._id === this.selectedCustomerClass.id_earning_points_product) || null;
    }
    else {
      this.selectedCustomerProgramBill = null;
      this.selectedCustomerProgramProduct = null;
    }

    this.pointValidity = this.vhQuerySales.getlocalCustomerPoints(id_customer);

    // Kiểm tra xem có phải hoá đơn được giảm giá hay không
    let check = this.vhQuerySales.getlocalDiscount_bill(this.selectedCustomerClass?.id_discount_bill);
    if (check) {
      this.isDiscountBill = true;
      this.vhComponent.alertMessageDesktop('success', this.languageService.translate("This customer applies the discount calculation formula ") + check.name, 3000);
    }
    else {
      this.isDiscountBill = false;
    }
  }



  /**
   * Tạo bill mới
   * @example this.newBill();
   * this.newBill().then((result: any) => { console.log(result); });
   * @returns Promise<any>
   */
  newBill(): Promise<any> {
    this.isAddingBill = true; // biến cờ để check mỗi lần chỉ add 1 bill dù bấm tạo đơn nhiều lần cùng lúc
    // thông tin bill default
    return new Promise((resolve, rejects) => {
      let bill = {
        bill_type: 5,
        payment_type: 1,
        id_customer: 'id_retail',
        tax_buyerDisplayName: 'Người mua không lấy hoá đơn',
        tax_buyerLegalName: '',
        tax_buyerTaxCode: '',
        tax_buyerAddressLine: '',
        tax_buyerEmail: '',
        id_shop: 'local',
        id_branch: this.vhQuerySales.getDefaultBranch()._id,
        id_employee: this.vhAuth.getUser()._id,
        id_wallet: 'id_cash',
        date: new Date(),
        total: 0,
        payment: 0,
        cqt_status : 1,
        tax: 0,
        discount: 0,
        fee: 0,
        note: '',
        price_type: 1,
        id_delivery: '',
        payment_points: 0,
        payment_coupons: 0,
        discount_bill: 0,
        earning_points_bill: 0,
        use_points: 0,
        cash: 0
      };

      if (!this.listBills.length) { // neu chưa có bill thì tạo rồi mới push
        this.vhQuerySales.createBill_Billdetail(bill, [])
          .then((bill: any) => {
            this.isAddingBill = false;
            this.listBills.push(bill);
            this.selectBill(bill, this.listBills.length - 1);
            resolve(true);
          }, (error: any) => {
            rejects(error);
          });
      }
      else {// neu đã có bill thì push trước rồi tạo mới
        this.listBills.push({ ...bill, bill_code: 'New bill', _id: '', id_shop: 'local' });
        this.vhQuerySales.createBill_Billdetail(bill, [])
          .then((bill: any) => {
            this.isAddingBill = false;
            this.listBills[this.listBills.length - 1]._id = bill._id;
            this.listBills[this.listBills.length - 1].bill_code = bill.bill_code;
            this.selectBill(bill, this.listBills.length - 1);
            resolve(true);
          }, (error: any) => {
            rejects(error);
          });
      }
    });
  }

  
  
