  /**
   * Khởi tạo Sản phẩm Best Selling
   */
  initProductsSelling() {
    this.productsBestSelling = this.vhQuerySales.getlocalDetailProducts(VhType.PRODUCT).filter(good => good.selling && !good.delete_hidden).slice(0, 12).map(item => {
      let unit = this.vhQuerySales.getUnit_byRatio(item.units, item.units.find(e => e.default) ? item.units.find(e => e.default).ratio : 1);
      if (item.type == VhType.PRODUCT && item.manylot) {
        item.lots = this.vhAlgorithm.sortDatebyASC(item.lots, 'date_exp');
        if (this.auto_hidden_lot) item.lots = item.lots.filter(e => new Date(e.date_exp).getTime() >= new Date().getTime());
        if (this.auto_select_lot && item.lots.length) {
          let min_date_exp = this.vhAlgorithm.sortDatebyASC(item.lots.filter(e => new Date(e.date_exp).getTime() >= new Date().getTime()), 'date_exp');
          if (min_date_exp.length) {
            item['id_lotproduct'] = min_date_exp[0]._id;
            item['lot_number'] = min_date_exp[0].lot_number;
          }
        }
      }
      return { ...item, ...unit };
    });
    let j = 0;
    for (let i = 0; i < this.productsBestSelling.length; i++) {
      if (!this.productsPaginationBestSelling[j])
        this.productsPaginationBestSelling[j] = this.productsPaginationBestSelling.length
          ? new Array(this.productsBestSelling[j + 5])
          : new Array(this.productsBestSelling[j]);
      else {
        this.productsPaginationBestSelling[j].push(this.productsBestSelling[i]);
      }
      if (this.productsPaginationBestSelling[j].length == 6) j++;
    }
  }



  /**
   * Khởi tạo Dịch vụ Best Selling
   */
  initServicesSelling() {
    this.servicesBestSelling = this.vhQuerySales.getlocalDetailProducts(VhType.SERVICE).filter(good => good.selling && !good.delete_hidden).slice(0, 12).map(item => { return { ...item, ...item.units[0] } });
    let k = 0;
    for (let i = 0; i < this.servicesBestSelling.length; i++) {
      if (!this.servicesPaginationBestSelling[k]) {
        this.servicesPaginationBestSelling[k] = this.servicesPaginationBestSelling.length ? new Array(this.servicesBestSelling[k + 5]) : new Array(this.servicesBestSelling[k]);
      }
      else {
        this.servicesPaginationBestSelling[k].push(this.servicesBestSelling[i]);
      }
      if (this.servicesPaginationBestSelling[k].length == 6) k++;
    }
  }



