  /**
   * Bắt sự kiện nhấn nút ok của modal chọn Lô
   */
  handleSelectLots() {
    // Nếu có cảnh báo hết hạn lô
    if (this.warning_lot_expried) {
      // Lấy lô được chọn
      let lot_select = this.listAllLots.find(e => e._id == this.tempProduct.id_lotproduct);
      // Kiểm tra xem lô đã hết hạn chưa
      if (new Date().getTime() >= new Date(lot_select.date_exp).getTime()) {
        this.nzModalService.confirm({
          nzTitle: this.languageService.translate("This lot has expired! Continue selling this product"),
          nzOnOk: () => {
            this.resetModalSelectLots();
            if (!this.tempProduct.id_product) this.chooseProduct(this.tempProduct);
          },
          nzOnCancel: () => {
          }
        });
      }
      else {
        this.resetModalSelectLots();
        if (!this.tempProduct.id_product) this.chooseProduct(this.tempProduct);
      }
    }
    else {
      this.resetModalSelectLots();
      if (!this.tempProduct.id_product) this.chooseProduct(this.tempProduct);
    }
  }



