  /**
   * Khi nhấnvào nút Khuyến mãi để lấy chương trình khuyến mãi
   */
  getPromotions() {
    this.isType7GiftSelected = false;
    this.vhComponent.showLoading('')
      .then(() => {
        /** Chứa các bill_detail có ptype = 6 */
        let bill_details_type_6;
        /** Chứa các bill_detail có ptype != 6 */
        let bill_details_others;
        // Nếu có Dịch vụ thời gian thì tách bill_detail có ptype = 6 ra để xử lý riêng, sau khi get xong thì thêm vô lại
        if(this.productService.isContainServiceTime(this.billDetails)) {
          bill_details_type_6 = this.billDetails.filter((item) => item.ptype === VhType.SERVICETIME);
          bill_details_others = this.billDetails.filter((item) => item.ptype !== VhType.SERVICETIME);
          this.billDetails = bill_details_others;
        }
        this.vhQuerySales.changeBillDetails_withPromotions(this.billDetails, this.selectedBill.date).then((bill_details_display: any) => {
          // Nếu có Dịch vụ thời gian thì thêm bill_detail có ptype = 6 vào lại
          if(bill_details_type_6) {
            bill_details_display.bill_details_pmtype_0to6 = [
              ...bill_details_type_6,
              ...bill_details_display.bill_details_pmtype_0to6,
            ];
          }
          this.handleChooseLotForPromotion(bill_details_display.bill_details_pmtype_0to6);
          this.handleChooseLotForPromotion(bill_details_display.bill_details_pmtype_7);

          this.promotionsType7 = this.vhAlgorithm.sortNumberbyASC([...bill_details_display.bill_details_pmtype_7], 'total_bill_value');
          this.vhComponent.hideLoading(0);

          // billDetails bây giờ là mảng chương trình khuyến mãi với các trường products[], products_gift[],... bên trong
          this.billDetails = [...bill_details_display.bill_details_pmtype_0to6];
          this.getEarningPointPromotion();
          this.handlePromotionType7(this.promotionsType7);
        }, error => {
          this.vhComponent.hideLoading(0).then(() => {
            this.vhComponent.showToast(5000, error, "alert-toast");
          });
        });
      });
  }



  /**
   * Xử lý chọn lô tự động cho sản phẩm chọn trong khuyến mãi
   * @param promotion array product promotion
   * @example this.handleChooseLotForPromotion([])
   */
  handleChooseLotForPromotion(promotion) {
    for (let i of promotion) {
      if (i.products_gift) {
        for (let product of i.products_gift) {
          if (product.lots && product.lots.length) {
            let min_date_exp = this.vhAlgorithm.sortDatebyASC(product.lots, 'date_exp');
            product['id_lotproduct'] = min_date_exp[0]._id;
            product['lot_number'] = min_date_exp[0].lot_number;
          }
          if (product.combos) {
            product.combos.forEach(element => {
              if (element.lots && element.lots.length) {
                let min_date_exp = this.vhAlgorithm.sortDatebyASC(element.lots, 'date_exp');
                element['id_lotproduct'] = min_date_exp[0]._id;
                element['lot_number'] = min_date_exp[0].lot_number;
              }
            });
          }
        }
      }
    }
  }



  /**
   * Hàm này để get SubTotal mới của mảng đã lấy quà
   * đồng thời gán điểm tích lũy sản phẩm mới của các sản phẩm trong khuyến mãi nếu có
   */
  getEarningPointPromotion() {
    for (let i of this.billDetails) {
      if (i.products_gift) {
        for (let product of i.products_gift) {
          product.earning_points_product = this.billService.getTotalEarningPointProduct(i, true, this.selectedCustomerProgramProduct);
        }
      }
      if (i.products) {
        for (let product of i.products) {
          product.earning_points_product = this.billService.getTotalEarningPointProduct(i, true, this.selectedCustomerProgramProduct);
        }
      }
      if (!i.id_promotion) {
        i.earning_points_product = this.billService.getTotalEarningPointProduct(i, null, this.selectedCustomerProgramProduct);
      }
    }
    this.updateBillCalculations();
  }



  /**
   * Xử lý khuyến mãi type 7 (tặng quà theo tổng giá trị đơn hàng), tìm ra khuyến mãi phù hợp với tổng tiền của bill và gán cho promotionsType7Show.
   *
   * Bằng cách lấy khuyến mãi cuối cùng trong danh sách, so sánh <= tổng tiền bill.
   *
   * @param promotionsType7 danh sách tất cả khuyến mãi type 7
   */
  handlePromotionType7(promotionsType7) {
    if (promotionsType7.length && promotionsType7.findLastIndex(e => e.total_bill_value <= this.getTotalATax()) != -1) {
      this.promotionsType7Show = [promotionsType7[promotionsType7.findLastIndex(e => e.total_bill_value <= this.getTotalATax())]];
    }
    else {
      this.promotionsType7Show = [];
    }
  }



