  /**
   * Mở modal Chỉnh sửa giá bán
   * @param product sản phẩm cần chỉnh sửa
   */
  openEditDiscountPrice(product: VhSaleInvoiceDetailEdit) {
    this.tempProduct = product;
    this.tempDiscountPrice = this.tempProduct.price;
    this.isShowEditDiscountPrice = true;
    setTimeout(() => {
      this.calculateDiscountValue();
    }, 0);
  }



  /**
   * Tính lại số tiền Giảm giá khi thay đổi Giá bán
   */
  calculateDiscountValue() {
    if (!document.querySelector('.new_discount_price input')) return;
    const priceOrigin = this.tempProduct.price_origin;
    let price = parseFloat(this.cleaveJsService.getRawValue('.new_discount_price input'));
    this.discountValue = this.productService.calculateDiscountValue(priceOrigin, price, this.default_discount_price_type);
  }



  /**
   * Tính lại Giá bán khi thay đổi Giảm giá
   */
  calculateDiscountPrice() {
    if (!document.querySelector('.new_discount_price_value input')) return;

    // Nếu là % thì giới hạn 100%
    if (this.default_discount_price_type === '%') {
      let price = parseFloat(this.cleaveJsService.getRawValue('.new_discount_price_value input'));
      if (price > 100) {
        this.discountValue = 100;
        (document.querySelector('.new_discount_price_value input') as HTMLInputElement).value = '100';
      }
    }

    const discountValue = this.cleaveJsService.getRawValue('.new_discount_price_value input');
    const priceOrigin = this.tempProduct.price_origin;
    this.tempDiscountPrice = this.productService.calculateDiscountPrice(priceOrigin, discountValue, this.default_discount_price_type, this.currencySymbol);
  }



  /**
   * Thay đổi kiểu giảm giá % hay currencySymbol
   * @param value kiểu gảm giá % hoặc currencySymbol
   */
  changeDiscountType(value) {
    this.default_discount_price_type = value;
    this.discountValue = 0;
    this.tempDiscountPrice = this.tempProduct.price_origin;
  }



  /**
   * Cập nhật lại giá
   */
  handleOkNewDiscountPrice() {
    const discountPrice = this.cleaveJsService.getRawValue('.new_discount_price input');
    if (discountPrice != '' && discountPrice >= 0) {
      this.tempProduct.price = parseFloat(discountPrice);
      this.isShowEditDiscountPrice = false;
      this.updateBillCalculations();
      this.billService.updateBillDetail(this.tempProduct._id, this.billService.getCleanBillDetail(this.tempProduct));
      if (!this.isType7GiftSelected) {
        this.handlePromotionType7(this.promotionsType7);
      }
    }
    else {
      this.mess.error(this.languageService.translate("Invalid data, please check again"));
    }
    this.cleaveJsService.destroyCleaves(['.new_discount_price input', '.new_discount_price_value input']);
  }



  /**
   * Tắt modal Thay đổi giá sản phẩm
   */
  handleCancelNewDiscountPrice() {
    this.isShowEditDiscountPrice = false;
    this.cleaveJsService.destroyCleaves(['.new_discount_price input', '.new_discount_price_value input']);
  }



