  /**
   * Khi thay đổi đơn vị của bill_detail
   * @param ratio ratio được chọn
   * @param product product của bill detail được chọn
   */
  changeUnit(ratio: number, product: any) {
    const unit = this.vhQuerySales.getUnit_byRatio(product.units, ratio);
    if (unit) {
      product.unit = unit.unit;
      product.price = this.default_price_type === 1 ? parseFloat(unit.price) : parseFloat(unit.price2);
      product.ratio = parseFloat(unit.ratio);
      product.price_origin = this.default_price_type === 1 ? parseFloat(unit.price) : parseFloat(unit.price2);
    }

    this.updateBillCalculations();
    this.billService.updateBillDetail(product._id, this.billService.getCleanBillDetail(product));
    if (!this.isType7GiftSelected) {
      this.handlePromotionType7(this.promotionsType7);
    }
  }



