import { ChangeDetectorRef, Component, ElementRef, EventEmitter, Input, Output, QueryList, SimpleChanges, ViewChild, ViewChildren, ViewContainerRef } from '@angular/core';
import { SalesRetailUI } from '../../sales-retail';
import { VhComponent } from 'src/app/sales/components/vh-component/vh-component';
import { LanguageService } from 'src/app/sales/services/language.service';
import { VhAlgorithm, VhAuth, VhQuery, VhQuerySales, VhEinvoice } from 'ionic-vhframeworks';
import { FunctionService } from 'src/app/sales/services/function.service';
import { NzModalService } from 'ng-zorro-antd/modal';
import { Platform } from '@ionic/angular';
import { Router } from '@angular/router';
import { NzMessageService } from 'ng-zorro-antd/message';
import { CustomerService } from 'src/app/sales/services/customer.service';
import { BillService } from 'src/app/sales/services/bill.service';
import { UserService } from 'src/app/sales/services/user.service';
import { ThemeService } from 'src/app/sales/services/theme.service';
import { PrintService } from 'src/app/sales/services/print.service';
import { ProductService } from 'src/app/sales/services/product.service';
import { CategoryService } from 'src/app/sales/services/category.service';
import { BranchService } from 'src/app/sales/services/branch.service';
import { SalesRetailSharedService } from '../../sales-retail-shared.service';
import { CleaveJsService } from 'src/app/sales/services/cleave-js.service';
import { fromEvent } from 'rxjs';
import { auditTime } from 'rxjs/operators';

@Component({
  selector: 'app-list-product-search',
  templateUrl: './list-product-search.component.html',
  styleUrls: ['./list-product-search.component.scss'],
})
export class ListProductSearchComponent extends SalesRetailUI {
  @Input() is_open: boolean;
  @Input() data: any;
  @ViewChild('listWrapper') listWrapper: ElementRef;
  @ViewChildren('itemWrapper', { read: ElementRef }) itemElements: QueryList<ElementRef>;
  @Output('close') close = new EventEmitter();

  isOpenModalAddProduct = false;

  itemHeight = 72;
  maxHeight = 400;
  startIndex = 0;
  endIndex = 0;
  totalHeight = 0;
  visibleHeight = 0;
  visibleItems: [];
  buffer = 5;

  constructor(
    protected salesSapoSharedService: SalesRetailSharedService,
    protected vhComponent: VhComponent,
    public languageService: LanguageService,
    public vhAuth: VhAuth,
    public vhQuerySales: VhQuerySales,
    protected router: Router,
    protected mess: NzMessageService,
    protected functionService: FunctionService,
    protected nzModalService: NzModalService,
    protected plaform: Platform,
    public vhAlgorithm: VhAlgorithm,
    protected branchService: BranchService,
    public categoryService: CategoryService,
    protected productService: ProductService,
    public themeService: ThemeService,
    public userService: UserService,
    public billService: BillService,
    protected customerService: CustomerService,
    protected cdr: ChangeDetectorRef,
    protected viewContainerRef: ViewContainerRef,
    protected vhQuery: VhQuery,
    protected printService: PrintService,
    protected cleaveJsService: CleaveJsService,
    protected vhEinvoice: VhEinvoice,
  ) {
    super(
      salesSapoSharedService, vhComponent, languageService, vhAuth, vhQuerySales, router, mess, functionService, nzModalService,
      plaform, vhAlgorithm, branchService, categoryService, productService, themeService, userService,
      billService, customerService, cdr, viewContainerRef, vhQuery, printService, cleaveJsService, vhEinvoice
    );
  }

  ngOnInit(): void {
    setTimeout(() => {
      this.changeTabMainSector(this.indexTabSearchProductMainSector);
    }, 0);
  }

  ngAfterViewInit() {
    fromEvent(this.listWrapper.nativeElement, 'scroll').pipe(
      auditTime(16)
    ).subscribe(() => this.updateVirtualList());

    // lần đầu render
    this.updateVirtualList();
    // mỗi khi có thay đổi element list
    this.itemElements.changes.subscribe(() => this.updateHeights());
  }

  ngOnChanges(changes: SimpleChanges): void {
    if (changes.data) {
      this.itemHeights = [];
      this.calculateItemTops();
      this.updateVirtualList();
    }
  }

  getDisplayedSellingPrice(item: any): string {
    let priceText = '';

    if (this.default_price_type == 1 || !item.price2) {
      priceText = this.vhAlgorithm.vhcurrencyunit(item.price);
    } else if (this.default_price_type == 2 && item.price2) {
      priceText = this.vhAlgorithm.vhcurrencyunit(item.price2);
    }

    if (item.type === this.VhType.SERVICETIME) {
      priceText += ` (${this.languageService.translate(item.unit)})`;
    }
    else if (item.type != 1) {
      priceText += ` (${item.unit})`;
    }

    return priceText;
  }

  // Add scroll handler
  onScroll(event: Event): void {
    this.updateVirtualList();
  }

  // Calculate visible items and total height
  updateVirtualList(): void {
    if (!this.is_open || !this.listWrapper?.nativeElement) return;
    const scrollTop = this.listWrapper.nativeElement.scrollTop;
    // find start index via binary search
    this.startIndex = this.findStartIndex(scrollTop);
    // find end index to cover viewport + buffer
    this.endIndex = this.findEndIndex(scrollTop);

    // slice visible items
    this.visibleItems = this.searchListSearchProduct.slice(this.startIndex, this.endIndex);

    // sau khi render DOM, đo lại heights
    setTimeout(() => {
      this.updateHeights();
    }, 0);
  }

  private findStartIndex(scrollTop: number): number {
    let lo = 0, hi = this.itemTops.length - 1;
    while (lo <= hi) {
      const mid = (lo + hi) >>> 1;
      if (this.itemTops[mid] < scrollTop) lo = mid + 1;
      else hi = mid - 1;
    }
    return Math.max(0, lo - this.buffer);
  }

  private findEndIndex(scrollTop: number): number {
    const viewportHeight = this.listWrapper.nativeElement.clientHeight;
    let boundary = scrollTop + viewportHeight;
    let lo = 0, hi = this.itemTops.length - 1;
    while (lo <= hi) {
      const mid = (lo + hi) >>> 1;
      if (this.itemTops[mid] < boundary) lo = mid + 1;
      else hi = mid - 1;
    }
    return Math.min(this.searchListSearchProduct.length, lo + this.buffer);
  }

  itemHeights: number[] = [];
  itemTops: number[] = [];

  private updateHeights(): void {
    let dirty = false;
    this.itemElements.forEach((elRef, idx) => {
      const realIdx = this.startIndex + idx;
      const h = elRef.nativeElement.offsetHeight;
      if (this.itemHeights[realIdx] !== h) {
        this.itemHeights[realIdx] = h;
        dirty = true;
      }
    });
    if (dirty) {
      this.calculateItemTops();
    }
  }

  calculateItemTops(): void {
    this.itemTops = [];
    let sum = 0;
    for (let i = 0; i < this.searchListSearchProduct.length; i++) {
      this.itemTops[i] = sum;
      sum += this.itemHeights[i] || this.itemHeight; // fallback if height not measured yet
    }
    this.totalHeight = sum;
    this.cdr.detectChanges();
  }

  getItemTop(index: number): number {
    return this.itemTops[index] || 0;
  }

}
