import { ChangeDetectorRef, Component, ElementRef, EventEmitter, Input, Output, ViewContainerRef } from '@angular/core';
import { SalesRetailUI } from '../../../sales-retail';
import { SalesRetailSharedService } from '../../../sales-retail-shared.service';
import { VhComponent } from 'src/app/sales/components/vh-component/vh-component';
import { LanguageService } from 'src/app/sales/services/language.service';
import { VhAlgorithm, VhAuth, VhQuery, VhQuerySales,VhEinvoice } from 'ionic-vhframeworks';
import { Router } from '@angular/router';
import { NzMessageService } from 'ng-zorro-antd/message';
import { FunctionService } from 'src/app/sales/services/function.service';
import { NzModalService } from 'ng-zorro-antd/modal';
import { Platform } from '@ionic/angular';
import { BranchService } from 'src/app/sales/services/branch.service';
import { CategoryService } from 'src/app/sales/services/category.service';
import { ProductService } from 'src/app/sales/services/product.service';
import { ThemeService } from 'src/app/sales/services/theme.service';
import { UserService } from 'src/app/sales/services/user.service';
import { BillService } from 'src/app/sales/services/bill.service';
import { CustomerService } from 'src/app/sales/services/customer.service';
import { PrintService } from 'src/app/sales/services/print.service';
import { CleaveJsService } from 'src/app/sales/services/cleave-js.service';

@Component({
  selector: 'app-search-customer',
  templateUrl: './search-customer.component.html',
  styleUrls: [
    './search-customer.component.scss',
    './search-customer-custom.component.scss',
  ]
})
export class SearchCustomerComponent extends SalesRetailUI {

  /** Có hiện hay không */
  @Input() visible: boolean;
  /** Nội dung ô tìm kiếm */
  @Input() searchValue: string;
  /** Khi đóng */
  @Output() onClose = new EventEmitter<any>();
  /** Khi nhấn nút mở from thêm Khách hàng */
  @Output() onShowAddCustomer = new EventEmitter<any>();

  /** Sẽ mở form Thêm khách hàng trong callbackAfterOKResetRetailCustomer hay không */
  callbackFrom: 'onAddCustomer' | 'onSelectCustomer' = 'onAddCustomer';
  /** Biến tạm lưu Khách hàng được chọn */
  tempCustomer: any;

  
  constructor(
    protected salesSapoSharedService: SalesRetailSharedService,
    protected vhComponent: VhComponent,
    public languageService: LanguageService,
    public vhAuth: VhAuth,
    public vhQuerySales: VhQuerySales,
    protected router: Router,
    protected mess: NzMessageService,
    protected functionService: FunctionService,
    protected nzModalService: NzModalService,
    protected plaform: Platform,
    public vhAlgorithm: VhAlgorithm,
    protected branchService: BranchService,
    public categoryService: CategoryService,
    protected productService: ProductService,
    public themeService: ThemeService,
    public userService: UserService,
    public billService: BillService,
    public customerService: CustomerService,
    protected cdr: ChangeDetectorRef,
    protected viewContainerRef: ViewContainerRef,
    protected vhQuery: VhQuery,
    protected printService: PrintService,
    protected element: ElementRef,
    protected cleaveJsService: CleaveJsService,
    protected vhEinvoice: VhEinvoice,
  ) {
    super(
      salesSapoSharedService, vhComponent, languageService, vhAuth, vhQuerySales, router, mess, functionService, nzModalService,
      plaform, vhAlgorithm, branchService, categoryService, productService, themeService, userService,
      billService, customerService, cdr, viewContainerRef, vhQuery, printService, cleaveJsService, vhEinvoice
    );
  }



  callbackAfterOKResetRetailCustomer() {
    if(this.callbackFrom === 'onAddCustomer') {
      this.onShowAddCustomer.emit();
    }
    else {
      // Nếu Thành viên sang Thành viên hoặc Khách lẻ chuyển sang Thành viên mà đã đồng ý clear
      this.changeCustomer(this.tempCustomer._id);
      this.onClose.emit();
    }
  }



  /**
   * Khi chọn khách hàng
   * @param customer khách hàng được chọn
   */
  onSelectCustomer(customer) {
    this.callbackFrom = 'onSelectCustomer';
    this.tempCustomer = customer;
    this.customerType = 2;
    // Khách lẻ chuyển sang Thành viên mà có thông tin Khách lẻ thì changeCustomerType(2)
    if(this.selectedBill.retail_name) {
      this.changeCustomerType(2);
    }
    else {
      // Nếu Thành viên sang Thành viên hoặc Khách lẻ chuyển sang Thành viên mà đã đồng ý clear
      this.changeCustomer(customer._id);
      this.onClose.emit();
    }
  }



  /**
   * Khi nhấn nút thêm khách hàng
   */
  onAddCustomer() {
    this.callbackFrom = 'onAddCustomer';
    const openForm = !this.selectedBill.retail_name || this.selectedBill.id_customer !== 'id_retail';
    // Khách lẻ chuyển sang Thành viên
    if(this.selectedBill.id_customer === 'id_retail') {
      this.changeCustomerType(2);
    }
    // Nếu chưa có Tên khách lẻ thì mở form thêm khách hàng (nếu có rồi sẽ được xử lí ở callbackAfterOKResetRetailCustomer)
    if(openForm) {
      this.onShowAddCustomer.emit();
    }
  }



  /**
   * Tìm kiếm khách hàng theo tên hoặc số điện thoại
   */
  searchKeyword_NamePhone() {
    const value = this.vhAlgorithm.changeAlias(this.searchValue.trim().toLowerCase());
    if (value.length >= 6) {
      this.vhQuerySales.searchKeyword_NamePhone('customers', value).then((res: any) => {
        this.customerList = res;
        if (this.customerList.length == 0) {
          this.vhComponent.alertMessageDesktop("warning", this.languageService.translate('Not found'));
        }
        if (this.customerList.length == 1) {
          this.onSelectCustomer(this.customerList[0]);
        }
      });
    }
  }



  /**
   * Lấy length của danh sách khách hàng
   * @returns length của danh sách khách hàng
   */
  get filteredCustomerListLength(): number {
    return this.customerList.filter(customer =>
      customer.name?.toLowerCase()?.includes(this.searchValue.toLowerCase()) ||
      customer.phone?.toLowerCase()?.includes(this.searchValue.toLowerCase())
    ).length;
  }

}
